import Util from '../utils'

export default {
  spring (student) {
    let newStudent = { ...student }

    newStudent['dob'] = newStudent['dobYear'].toString().padStart(4, '0') + '-' + newStudent['dobMonth'].toString().padStart(2, '0') + '-' + newStudent['dobDay'].toString().padStart(2, '0')
    // newStudent.dob = Util.dateConversion(newStudent['dobYear'].toString().padStart(4, '0') + '/' + newStudent['dobMonth'].toString().padStart(2, '0') + '/' + newStudent['dobDay'].toString().padStart(2, '0'))
    // delete newStudent['dobYear']
    // delete newStudent['dobMonth']
    // delete newStudent['dobDay']

    return newStudent
  },

  vue (student) {
    let newStudent = { ...student }

    let splitedDOB = newStudent['dob'].split('-')
    // if (newStudent['bsDOB']) { splitedDOB = newStudent['bsDOB'].split('-') } else { splitedDOB = AD2BS(newStudent['dob']).split('-') }

    newStudent['dobYear'] = splitedDOB[0]
    newStudent['dobMonth'] = splitedDOB[1]
    newStudent['dobDay'] = splitedDOB[2]
    newStudent['subjectCount'] = newStudent['studentHasSubjectsById'].length

    return newStudent
  }
}



