<template>

  <v-flex md6>
    <v-form v-model="valid" ref="notice_form">

      <v-text-field
        v-model="notice.title"
        :rules="titleRules"
        :counter="30"
        label="Name"
        required
        solo
      ></v-text-field>

      <v-text-field
        v-model="notice.publishedDate"
        :rules="titleRules"
        :counter="30"
        label="Published Date"
        required
        solo
      ></v-text-field>

      <v-textarea
        v-model="notice.content"
        :rules="contentRules"
        :counter="30"
        label="Content"
        required
        rows="20"
        solo
      ></v-textarea>


      <v-btn
        :disabled="!valid"
        @click="submit"
      >
        submit
      </v-btn>
      <v-btn @click="clear">clear</v-btn>
    </v-form>
  </v-flex>

</template>

<script>
  import NoticeApi from '../../../api/notices'
export default {
  name: 'notice-form',

  props: {
    notice: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      valid: false,
      name: '',
      id: '',
      titleRules: [
        v => !!v || 'title is required',
      ],
      contentRules: [
        v => !!v || 'Content is required',
      ]
    }
  },

  methods: {
    clear () {
      this.$refs.notice_form.reset()
    },

    submit () {
      NoticeApi.save(this.notice).then((response) => {
        this.$router.push({ name: 'admin.notices.index' })
      })
    }
  }
}
</script>

<style scoped>

</style>



