export default {
  spring (centre) {
    let newCentreHasSchools = []
    centre.centreHasSchoolsById.forEach((item) => {
      newCentreHasSchools.push({
        id: item.id,
        schoolId: item.schoolId,
        centreId: item.centreId,
        supplementary: item.supplementary
      })
    })
    let newCentre = { ...centre }

    delete newCentre['schoolList']

    newCentre.centreHasSchoolsById = newCentreHasSchools
    return newCentre
  },

  vue (centre) {
    centre.centreHasSchoolsById = centre.centreHasSchoolsById.map(item => {
      item.centreHasSchoolList = [item.schoolsBySchoolId]
      return item
    })

    console.log(centre)
    centre.schoolList = [centre.schoolsBySchoolId]

    return centre
  }
}



