<template>

  <v-layout>
    <v-flex md5>
      <v-form v-model="valid" ref="discipline_form">

        <v-text-field
          v-model="discipline.name"
          :rules="nameRules"
          :counter="30"
          label="Name"
          required
          class="my-text-uppercase"
        ></v-text-field>

        <v-checkbox
          v-model="discipline.isTechnical"
          label="Is Technical"
        ></v-checkbox>

        <v-text-field
          v-model="discipline.subjectCount"
          type="number"
          label="Subject Count"
          @change="subjectCountChangeHandler"
          :rules="subjectCountRules"
        ></v-text-field>

        <v-autocomplete
          v-for="counter in subjectCounter "
          :key="counter"
          v-model="discipline.disciplineHasSubjectsById[counter]"
          :items="subjects"
          return-object
          chips
          :label="'Subject for position ' + counter "
          item-text="name"
          multiple
          @change="changeHandler"
          :rules="subjectRules"
        >
          <template
            slot="selection"
            slot-scope="data"
          >
            <v-chip
              :selected="data.selected"
              close
              class="chip--select-multi"
              @input="removeDisciplineHasSubject(data.item, counter)"
            >
              {{ data.item.name }}
            </v-chip>
          </template>
          <template
            slot="item"
            slot-scope="data"
          >

              <template v-if="typeof data.item !== 'object'">
                <v-list-tile-content v-text="data.item"></v-list-tile-content>
              </template>
              <template v-else>
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>

        </v-autocomplete>

        <v-btn
          :disabled="!valid"
          @click="submit"
        >
          submit
        </v-btn>
        <v-btn @click="clear">clear</v-btn>
      </v-form>
    </v-flex>
    <v-flex md5 offset-md1>
      <v-text-field
        v-model="discipline.regularSymbolNoStarting"
        :counter="1"
        label="Regular SymbolNo Starting"
        required
        :rules="startingNumberRules"
      ></v-text-field>

      <v-text-field
        v-model="discipline.exemptSymbolNoStarting"
        :counter="1"
        label="Exempted SymbolNo Starting"
        :rules="startingNumberRules"
      ></v-text-field>

    </v-flex>
  </v-layout>
</template>

<script>
import DisciplineApi from '../../../api/disciplines'
import SubjectApi from '../../../api/subjects'
import DisciplineTansformer from '../../../transformers/discipline'
import Bus from '../../../Bus'

export default {
  name: 'discipline-form',

  created () {
    Bus.$on('admin.discipline.disabled-subjects', () => {
      let selectedSubjects = []
      for (let key in this.discipline.disciplineHasSubjectsById) {
        this.discipline.disciplineHasSubjectsById[key].forEach((item) => {
          selectedSubjects.push(item.id)
        })
      }
      selectedSubjects.forEach((subjectId) => {
        this.subjects.forEach((item) => {
          if (item.id === subjectId) { item.disabled = true }
        })
      })
    })
    SubjectApi.getAllSubjectGroupBySubjectOrder().then((response) => {
      let newSubjects = []

      for (let key in response.body) {
        newSubjects.push({ header: 'Subject Order ' + key })
        response.body[key].forEach((innerItem) => {
          newSubjects.push(innerItem)
        })
        newSubjects.push({ divider: true })
      }

      this.subjects = newSubjects
      if (this.$route.params.id) Bus.$emit('admin.discipline.disabled-subjects')
    })
  },

  watch: {
    discipline (oldDiscipline, newDiscipline) {
      if (oldDiscipline.subjectCount !== newDiscipline.subjectCount) {
        this.subjectCountChangeHandler();
        if (this.$route.params.id) Bus.$emit('admin.discipline.disabled-subjects')
      }
    }
  },

  props: {
    discipline: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      valid: false,
      subjects: [],
      subjectCounter: [],
      name: '',
      id: '',
      selectedSubjects: new Set(),
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 30) || 'Name must be less than 30 characters'
      ],

      startingNumberRules: [
        v => !!v || 'Name is required'
      ],

      subjectCountRules: [
        v => !!v || 'Subject Count is required',
        v => (v && parseInt(v) < 13) || 'Subject Count must be less than 13'
      ],

      subjectRules: [
        v => !!v || 'At least one Subject is required',
        v => (v && v.length > 0) || 'At least one Subject is required'
      ]
    }
  },

  methods: {
    clear () {
      this.$refs.discipline_form.reset()
    },

    subjectCountChangeHandler () {
      let disciplineHasSubjectsById = {}

      this.subjectCounter = []

      for (let counter = 1; counter <= this.discipline.subjectCount; counter++) {
        disciplineHasSubjectsById[counter] = this.discipline.disciplineHasSubjectsById[counter] ? this.discipline.disciplineHasSubjectsById[counter] : []
        this.subjectCounter.push(counter)
      }

      this.discipline.disciplineHasSubjectsById = disciplineHasSubjectsById
    },

    removeDisciplineHasSubject (item, counter) {
      let filterItems = this.discipline.disciplineHasSubjectsById[counter].filter(dhs => dhs.id === item.id)
      for (let filterItem of filterItems) {
        this.discipline.disciplineHasSubjectsById[counter].splice(this.discipline.disciplineHasSubjectsById[counter].indexOf(filterItem), 1)
      }
      item.disabled = false
    },

    submit () {
      let newDiscipline = { ...this.discipline }
      newDiscipline.disciplineHasSubjectsById = DisciplineTansformer.tansformDisciplineHasSubject(newDiscipline.disciplineHasSubjectsById)
      DisciplineApi.save(newDiscipline).then((response) => {
        this.$router.push({ name: 'admin.disciplines.index' })
      })
    },

    changeHandler (items) {
      console.log(items, 'hello')
      items.forEach((item) => {
        item.disabled = true
      })
    }
  }
}
</script>

<style scoped>

</style>



