<template>

  <v-form v-model="valid" ref="centre_form">

    <v-dialog
      v-model="deleteDialog"
      max-width="400"
    >
      <v-card>
        <v-card-title class="headline">Are you sure?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteDialog = false"
          >
            No
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="removeItem"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-layout>
      <v-flex md8>
        <v-autocomplete
          v-model="centre.schoolId"
          @input.native="loadForSchoolSelection"
          :items="centre.schoolList"
          :item-text="showingText"
          item-value="id"
          label="Centre School"
          single-line
          v-debounce="1000"
          md4
        ></v-autocomplete>


        <v-text-field v-model="centre.centreCode" label="Centre Code" disabled></v-text-field>

        <v-btn style="float: right" @click="addCentreHasSchool">Add School Inside Centre</v-btn>

        <v-data-table
          :headers="tableHeaders"
          :items="centre.centreHasSchoolsById"
          class="elevation-2 mt20"
          hide-actions
        >
          <template slot="headerCell" slot-scope="props">
            <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
              <span>
          {{ props.header.text }}
        </span>
            </v-tooltip>
          </template>
          <template slot="items" slot-scope="props">
              <td>
                <v-autocomplete
                  v-model="props.item.schoolId"
                  @input.native="loadForCentreHasSchoolSelection($event, props.index)"
                  :items="props.item.centreHasSchoolList"
                  :item-text="showingText"
                  item-value="id"
                  label="School inside Centre"
                  v-debounce="1000"
                ></v-autocomplete>
              </td>
              <td>
                <v-checkbox
                  v-model="props.item.supplementary"
                ></v-checkbox>
              </td>

              <td>
                <v-btn @click="showDeleteDialog(props.index)" class="error"><v-icon>close</v-icon></v-btn>
              </td>
          </template>
        </v-data-table>

        <div class="mt20">
          <v-btn :disabled="!valid"
                 @click="submit"
          >
            submit
          </v-btn>
          <v-btn @click="clear">clear</v-btn>
        </div>
      </v-flex>
    </v-layout>
  </v-form>
</template>

<script>
import CentreApi from '../../../api/centres'
import Util from '../../../utils'
import debounce from 'v-debounce'
import CentreTransformer from '../../../transformers/centres'

export default {
  name: 'centre-form',

  created () {
  },

  props: {
    centre: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      valid: false,
      name: '',
      id: '',
      centreCodeRules: [
        v => !!v || 'Centre Code is required',
        v => (v && v.length <= 4) || 'Centre Code must be less than 4 characters'
      ],
      tableHeaders: [
        { text: 'Name', value: 'name', width: '85%', sortable: false },
        { text: 'Supp', value: 'name', width: '5%', sortable: false },
        { text: 'Actions', value: 'action', sortable: false }
      ],
      deleteItems: [],
      deleteDialog: false,
      deletingItem: null
    }
  },

  methods: {

    removeItem () {
      let index = this.deletingItem
      if (this.centre.centreHasSchoolsById[index].id) {
        CentreApi.deleteChsById(this.centre.centreHasSchoolsById[index].id).then((res) => {
          this.centre.centreHasSchoolsById.splice(index, 1)
          this.deleteDialog = false
        })
      } else {
        this.centre.centreHasSchoolsById.splice(index, 1)
        this.deleteDialog = false
      }
    },
    showingText (item) {
      return item.name + ' (' + item.id + ')'
    },
    clear () {
      this.$refs.centre_form.reset()
    },

    addCentreHasSchool () {
      this.centre.centreHasSchoolsById.push({
        schoolId: null,
        centreId: null,
        supplementary: false,
        centreHasSchoolList: []
      })

      // this.centreHasSchoolList[this.centre.centreHasSchoolsById.length - 1] = []
    },

    loadForSchoolSelection (event) {
      if (event.target.value.length > 3) {
        let ids = this.centre.centreHasSchoolsById.map((item) => item.schoolId)
        CentreApi.searchSchoolForCentreCreation({
          search: event.target.value.toUpperCase(),
        }).then((response) => {
          this.centre.schoolList = response.body
        })
      }
    },

    loadForCentreHasSchoolSelection (event, index) {
      console.log(event)
      if (event.target.value.length > 3) {
        CentreApi.searchSchoolForCentreHasSchoolCreation({
          search: event.target.value.toUpperCase()
        }).then((response) => {
          this.centre.centreHasSchoolsById[index].centreHasSchoolList = response.body
        })
      }
    },

    disciplineDoneBtn () {
      this.$refs.discipline.isMenuActive = false
      this.$refs.discipline.isFocused = false
      this.goTo('address')
    },

    submit () {
      CentreApi.save(CentreTransformer.spring(this.centre)).then((response) => {
        this.$router.push({ name: 'admin.centres.index' })
      }).catch((err) => {
        if (err) {
          alert(err.body.message)
        }
      })
    },

    showDeleteDialog (item) {
      this.deletingItem = item
      this.deleteDialog = true
    },

    fetchNewSchoolId () {
      // SchoolApi.getNextSchoolId(this.school.locationWardId).then((response) => {
      //   this.school.id = response.body.schoolId
      // })
    }
  },
  directives: { debounce }
}
</script>

<style scoped>
    .mt20 {
      margin-top: 20px;
    }
</style>



