<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Centres</h2>
      </v-flex>

      <v-flex md4>
        <v-text-field
          v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-flex>
      <v-flex justify-end>
        <v-btn fab :to="{ name: 'admin.centres.create' }" class="float-right"><v-icon>add</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <v-dialog
      v-model="deleteDialog"
      max-width="400"
    >
      <v-card>
        <v-card-title class="headline">Are you sure?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteDialog = false"
          >
            No
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteItem"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-data-table
      :headers="headers"
      :items="centres"
      class="elevation-1"
      :search="search"
      :custom-filter="searchFunc"
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ props.index + 1 }}</td>
        <td>{{ props.item.centreCode }}</td>
        <td>{{ props.item.schoolsBySchoolId.name }}[{{ props.item.schoolsBySchoolId.id }}]  ({{ props.item.schoolsBySchoolId.address }})</td>
        <td>
          <p v-for="(school, index) in props.item.centreHasSchoolsById">{{ index + 1}}) {{ school.schoolsBySchoolId.name }}[{{ school.schoolsBySchoolId.id }}] ({{ school.schoolsBySchoolId.address }})</p>
        </td>
        <td>
          <v-btn danger flat @click=" showDeleteDialog(props.item) "><v-icon>delete</v-icon></v-btn>
          <v-btn :to="{ name: 'admin.centres.edit', params: { id: props.item.id } }" primary flat><v-icon>create</v-icon></v-btn>
        </td>
      </template>
    </v-data-table>

  </div>

</template>

<script>

import CentreApi from '../../../api/centres'
import CentreForm from './Form'

export default {

  components: { CentreForm },
  mounted () {
    this.getDataFromApi()
  },

  watch: {
    pagination: {
      handler () {
        this.getDataFromApi()
      },
      deep: true
    }
  },
  data () {
    return {
      dialog: false,
      loading: true,
      pagination: {},
      totalSchools: 0,
      centres: [],
      search: '',
      deletingItem: null,
      deleteDialog: false,
      headers: [
        {
          text: 'SN',
          align: 'left',
          sortable: false,
          value: 'sn'
        },

        {
          text: 'Code',
          align: 'left',
          sortable: true,
          value: 'centreCode'
        },
        { text: 'Name', value: 'schoolsBySchoolId.name' },
        { text: 'School List', value: 'schoolList' },
        { text: 'Actions', value: 'Actions' }
      ]
    }
  },
  methods: {
    getDataFromApi () {
      this.loading = true
      return CentreApi.index(this.pagination).then((response) => {
        this.centres = response.body
      })
    },

    searchFunc () {
      return this.centres.filter((item) => {
        let q = this.search.toUpperCase()
        return item.schoolsBySchoolId.name.search(q) !== -1 || item.centreCode.toString().search(q) !== -1
      })
    },

    deleteItem () {
      CentreApi.delete(this.deletingItem.id).then((response) => {
        let index = this.centres.indexOf(this.deletingItem)
        this.centres.splice(index, 1)
      })

      this.deleteDialog = false
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },

    showDeleteDialog (item) {
      this.deletingItem = item
      this.deleteDialog = true
    }
  }
}
</script>

<style scoped>

</style>



