<template>
  <div>
    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit Discipline</h2>
      </v-flex>
      <v-flex md2>
        <v-btn fab :to="{ name: 'admin.disciplines.index' }"  class="float-right" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <discipline-form :discipline="blankDiscipline" ></discipline-form>
  </div>
</template>

<script>
import DisciplineApi from '../../../api/disciplines'
import DisciplineForm from './Form'
import DisciplineTansformer from '../../../transformers/discipline'
import Bus from '../../../Bus'
import Util from '../../../utils'


export default {
  components: { DisciplineForm },
  data () {
    return {
      blankDiscipline: {
        disciplineHasSubjectsById: {}
      }
    }
  },

  watch: {
    '$route' (to, from) {
      this.created()
    }
  },

  created () {
    DisciplineApi.findById(this.$route.params.id).then((response) => {
      this.blankDiscipline = response.body
      this.blankDiscipline.disciplineHasSubjectsById = DisciplineTansformer.tansformDisciplineHasSubjectVue(this.blankDiscipline.disciplineHasSubjectsById )
      Util.sleep(300).then(() => Bus.$emit('admin.discipline.disabled-subjects'))
    })
  }
}
</script>



