import Vue from 'vue'

export default {
  index (query) {
    return Vue.http.get('v1/centres', query)
  },

  save (school) {
    return Vue.http.post('v1/centres', school)
  },

  delete (centreId) {
    return Vue.http.delete('v1/centres/' + centreId)
  },

  getNextCentreCode (wardId) {
    return Vue.http.get('v1/centres/generate-id', { params: { wardId } })
  },

  findById (centreId) {
    return Vue.http.get('v1/centres/' + centreId)
  },

  searchSchoolForCentreCreation (query) {
    return Vue.http.get('v1/centres/search-for-centre-creation', { params: query })
  },

  searchSchoolForCentreHasSchoolCreation (query) {
    return Vue.http.get('v1/centres/search-for-centre-has-school-creation', { params: query })
  },

  deleteChsById(id) {
    return Vue.http.delete('v1/centres/chs/' + id)
  }
}



