<template>

  <div>

    <v-layout row wrap mb40 style="margin-bottom: 20px">
      <v-flex md2 mb20>
        <h2>Subjects</h2>
      </v-flex>

      <v-flex md4>
        <v-text-field
          v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-flex>
      <v-flex justify-end>
        <v-btn v-if=" $auth.user().username === 'superuser' " fab :to="{ name: 'admin.subjects.create' }"
               class="float-right">
          <v-icon>add</v-icon>
        </v-btn>
      </v-flex>
    </v-layout>

    <v-dialog
      v-model="deleteDialog"
      max-width="400"
    >
      <v-card>
        <v-card-title class="headline">Are you sure?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteDialog = false"
          >
            No
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteItem"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="schoolListDialog"
      max-width="600"
    >
      <v-layout style="background-color: white">
        <v-flex md12>

          <v-card>
            <v-card-title class="headline">School List</v-card-title>
          </v-card>

          <v-data-table
            :headers="schoolHeader"
            :items="schoolList"
            class="elevation-1"
            :rows-per-page-items="[-1]"
            hide-actions
            style="margin: 10px"
          >
            <template slot="items" slot-scope="props">
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.name }}</td>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>

      <v-card>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary darken-1" flat @click="schoolListDialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-data-table
      :headers="headers"
      :items="items"
      class="elevation-1"
      :rows-per-page-items="[-1]"
      :search="search"
      hide-actions
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ props.index + 1 }}</td>

        <td>{{ props.item.id }}</td>
        <td>{{ props.item.name }}</td>
        <td>{{ props.item.subjectShortcode }}</td>
        <td>
          <v-icon v-if="props.item.hasPractical">check</v-icon>
          <v-icon v-else>close</v-icon>
        </td>
        <td>{{ props.item.fullMarks }}</td>
        <!--<td>{{ props.item.passMarks }}</td>-->
        <td>{{ props.item.creditHour }}</td>
        <td>{{ props.item.prFullMarks }}</td>

        <td>
          <v-btn color="primary" dark @click="loadSchoolData(props.item.id)">Listing</v-btn>
        </td>
        <td v-if=" $auth.user().username === 'superuser' ">
          <v-btn danger xs flat @click=" showDeleteDialog(props.item) " icon>
            <v-icon>delete</v-icon>
          </v-btn>
          <v-btn :to="{ name: 'admin.subjects.edit', params: { id: props.item.id } }" xs primary flat icon>
            <v-icon>create</v-icon>
          </v-btn>
        </td>
      </template>
    </v-data-table>

  </div>

</template>

<script>

import SubjectApi from '../../../api/subjects'

export default {
  created () {
    SubjectApi.index().then((response) => {
      this.items = response.body
    })

    if (this.$auth.user().username === 'superuser') {
      this.headers.push({ text: 'Actions', value: 'actions', width: '30%', sortable: false })
    }
  },

  data () {
    return {
      search: '',
      schoolHeader: [
        {
          text: 'Code',
          align: 'left',
          sortable: true,
          value: 'id',
          width: '5%'
        },
        {
          text: 'Name',
          align: 'left',
          sortable: true,
          value: 'name',
          width: '95%'
        }
      ],
      headers: [
        {
          text: 'SN',
          align: 'left',
          sortable: false,
          value: 'SN'
        },
        {
          text: 'Subject Code',
          align: 'left',
          sortable: true,
          value: 'id',
          width: '5%'
        },
        { text: 'Name', value: 'name', width: '40%' },
        { text: 'Short Name', value: 'subjectShortcode', width: '5%' },
        { text: 'Has Practical', value: 'hasPractical', width: '3%' },
        { text: 'Full Marks', value: 'fullMarks', width: '3%' },
        { text: 'Credit Hour', value: 'creditHour',  width: '3%' },
        { text: 'Pr Full Marks', value: 'prFullMarks', width: '3%' },
        { text: 'School', value: 'actions', width: '3%' }
      ],
      items: [],
      deletingItem: null,
      deleteDialog: false,
      schoolList: [],
      schoolListDialog: false
    }
  },

  methods: {
    deleteItem () {
      SubjectApi.delete(this.deletingItem.id).then((response) => {
        let index = this.items.indexOf(this.deletingItem)
        this.items.splice(index, 1)
        this.deleteDialog = false
      }).catch((err) => {
        if (err) {
          alert('Subject is attached with other entity')
          console.log(err)
        }
      })
    },
    loadSchoolData (subjectId) {
      SubjectApi.getDataForSchoolListingOnSubject(subjectId).then((response) => {
        this.schoolList = response.body
        this.schoolListDialog = true
      })
    },
    showDeleteDialog (item) {
      this.deletingItem = item
      this.deleteDialog = true
    }
  }
}
</script>

<style scoped>

</style>



