import Vue from 'vue'

export default {
  index () {
    return Vue.http.get('v1/admin/routines')
  },

  save (data) {
    return Vue.http.post('v1/admin/routines', data)
  },

  publicIndex () {
    return Vue.http.get('v1/public/routines')
  },

  async download (url = 'v1/public/routines/download', filename = 'exam-routine.pdf', params = {}) {
    let response = await Vue.http.get(url, { params: params, responseType: 'arraybuffer' })
    let blob = new Blob([response.data], { type: response.headers.get('content-type') })
    let link = document.createElement('a')
    link.href = window.URL.createObjectURL(blob)
    link.download = filename
    link.click()
  }
}



