<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Practical Marks</h2>
      </v-flex>
    </v-layout>

    <v-data-table
      :headers="headers"
      :items="entries"
      class="elevation-1"
      hide-actions
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ props.item.id }}</td>
        <td>{{ props.item.fullName }}</td>
        <td>{{ props.item.symbolNo }}</td>
        <td v-for="subject in subjectHeader" v-if="props.item.studentHasSubjectsById[subject.value]">
          {{ props.item.studentHasSubjectsById[subject.value].prMarks }}
        </td>
        <td v-else>
          NA
        </td>
      </template>
    </v-data-table>

  </div>

</template>

<script>
import PracticalMarksApi from '../../../api/practical-marks'
export default {
  name: 'mark-list',

  created () {
    PracticalMarksApi.getMarksList().then((res) => {
      this.entries = this.mapData(res.body.students)
      this.subjects = res.body.subjects

      this.generateHeader()
    })
  },

  data () {
    return {
      entries: [],
      partialHeaders: [
        {
          text: 'Registration Number',
          align: 'left',
          sortable: true,
          value: 'id',
          width: '7%'
        },
        {
          text: 'Name',
          align: 'left',
          sortable: true,
          value: 'fullName',
          width: '10%'
        },
        {
          text: 'Symbol Number',
          align: 'left',
          sortable: true,
          value: 'symbolNo',
          width: '7%'
        }
      ],
      subjectHeader: [],
      subjects: [],
      headers: []
    }
  },

  methods: {
    generateHeader () {
      this.subjectHeader = []
      this.subjects.forEach(subject => {
        this.subjectHeader.push({
          text: subject.subjectShortcode,
          value: subject.id,
          sortable: false,
          width: '3%'
        })
      })
      this.headers = this.partialHeaders.concat( this.subjectHeader )
    },

    mapData (data) {
      return data.map((student) => {
        let subjects = {}
        student.studentHasSubjectsById.forEach((subject) => {
          subjects[subject.subjectId] = subject
        })

        student.studentHasSubjectsById = subjects

        return student
      })
    },
  }
}
</script>

<style scoped>

</style>



