<template>
  <div>
    <div v-show="$store.state.core.metas.is_installed === '0'">

      <v-form v-model="valid" ref="subject_form">
        <v-layout>
          <v-flex md4>
            <p class="display-1 font-weight-light">Installation</p>
            <v-text-field type="number" v-model="installationData.noOfWards" label="No of Wards"></v-text-field>
            <v-btn @click="install">Submit</v-btn>
          </v-flex>
        </v-layout>
      </v-form>
    </div>

    <v-layout row mb40>

      <v-flex md6>
        <h2 style="margin-bottom: 20px">Ward Wise Details</h2>
        <v-data-table
          :headers="wardWiseHeaders"
          :items="wardWiseTable.wards"
          class="elevation-1"
          hide-actions
          style="text-align: center;padding: 0px !important;"
        >
          <template slot="headers" slot-scope="props">
            <tr>
              <th>WARD</th>
              <th colspan="3">SCHOOLS</th>
              <th colspan="3">STUDENTS</th>
            </tr>
            <tr>
              <th></th>
              <th>Community</th>
              <th>Institutional</th>
              <th>Total</th>

              <th>Male</th>
              <th>Female</th>
              <th>Total</th>
            </tr>
          </template>
          <template slot="items" slot-scope="props">
            <td>{{ props.item.id }}</td>

            <td>{{ wardWiseTable.school[props.item.id] && wardWiseTable.school[props.item.id].public_s ?
              wardWiseTable.school[props.item.id].public_s : 0 }}
            </td>
            <td>{{ wardWiseTable.school[props.item.id] && wardWiseTable.school[props.item.id].private_s ?
              wardWiseTable.school[props.item.id].private_s : 0 }}
            </td>
            <td>{{ wardWiseTable.school[props.item.id] && wardWiseTable.school[props.item.id].total ?
              wardWiseTable.school[props.item.id].total : 0 }}
            </td>

            <td>{{ wardWiseTable.student[props.item.id] && wardWiseTable.student[props.item.id].male ?
              wardWiseTable.student[props.item.id].male : 0 }}
            </td>
            <td>{{ wardWiseTable.student[props.item.id] && wardWiseTable.student[props.item.id].female ?
              wardWiseTable.student[props.item.id].female : 0 }}
            </td>
            <td>{{ wardWiseTable.student[props.item.id] && wardWiseTable.student[props.item.id].total ?
              wardWiseTable.student[props.item.id].total : 0 }}
            </td>

          </template>

          <template slot="footer">
            <tr>
              <td><b>Total</b></td>
              <td>{{ publicTotalSchoolCount }}</td>
              <td>{{ privateTotalSchoolCount }}</td>
              <td>{{ totalSchoolCount }}</td>
              <td>{{ maleTotalStudentCount }}</td>
              <td>{{ femaleTotalStudentCount }}</td>
              <td>{{ totalStudentCount }}</td>
            </tr>
          </template>
        </v-data-table>
      </v-flex>

      <v-flex offset-md1 md5 class="width-100">
        <v-tabs
          v-model="tabActive"
        >
          <v-tab
            :key="0"
            ripple
          >
            School Chart
          </v-tab>

          <v-tab
            :key="1"
            ripple
          >
            Student Chart
          </v-tab>

          <v-tab-item
            :key="0"
          >
            <ward-chart-school v-if="wardWiseTable.wards.length > 0" v-model="wardWiseTable"></ward-chart-school>
          </v-tab-item>

          <v-tab-item
            :key="1"
          >
            <ward-chart-student v-if="wardWiseTable.wards.length > 0" v-model="wardWiseTable"></ward-chart-student>
          </v-tab-item>
        </v-tabs>
      </v-flex>
    </v-layout>
    <p style="page-break-after: always"><span></span></p>
    <v-layout row mb40>

      <v-flex xs12 md6 d-flex>
        <h2 style="margin-bottom: 20px">Grade Wise Details</h2>
        <v-select
          :items="refinedWards"
          v-model="selectedWardForTable"
          label="WARD"
          outline
          @change="wardChangeForGradeTable"
        ></v-select>
      </v-flex>

      <v-flex xs12 md6 d-flex>
      </v-flex>
    </v-layout>

    <v-layout row wrap mb40>

      <v-flex md6>

        <v-data-table
          :headers="gradeWiseHeaders"
          :items="ranges"
          class="elevation-1"
          hide-actions
          style="text-align: center;padding: 0px !important;"
        >
          <template slot="items" slot-scope="props">
            <td>{{ props.item }}</td>
            <td>{{ gradeWiseData.male && gradeWiseData.male[props.item] ? gradeWiseData.male[props.item] : 0 }}</td>
            <td>{{ gradeWiseData.female && gradeWiseData.female[props.item] ? gradeWiseData.female[props.item] : 0 }}</td>
            <td>{{ (gradeWiseData.female && gradeWiseData.female[props.item] ? gradeWiseData.female[props.item] :  0) + ( gradeWiseData.male && gradeWiseData.male[props.item] ? gradeWiseData.male[props.item] : 0 ) }}</td>
          </template>
        </v-data-table>
      </v-flex>
      <p style="page-break-after: always"><span></span></p>
      <v-flex offset-md1 md5>
        <grade-pie-chart :chartData="dataForPie"></grade-pie-chart>
      </v-flex>

    </v-layout>

    <v-layout style="margin-top: 20px" class="no-print">
      <h2>Download Best School Report</h2>
    </v-layout>
    <v-layout class="no-print">
      <v-flex md1>
        <v-select
          :items="refinedWardsForReport"
          v-model="reportParameters.ward"
          label="WARD"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-select
          :items="genderValues"
          v-model="reportParameters.sex"
          label="GENDER"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-select
          :items="schoolTypes"
          v-model="reportParameters.schoolType"
          label="SCHOOL TYPE"
          item-text="name"
          item-value="id"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-select
          :items="subjects"
          v-model="reportParameters.subject"
          label="SUBJECT"
          item-text="name"
          item-value="id"
          outline
        ></v-select>
      </v-flex>


      <v-flex offset-md1 md2>
        <v-select
          :items="thType"
          v-model="reportParameters.thType"
          label="MARKS TYPE"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-btn @click="downloadReport">DOWNLOAD</v-btn>
      </v-flex>

    </v-layout>


    <v-layout style="margin-top: 20px" class="no-print">
      <h2>Download Best Student Report</h2>
    </v-layout>
    <v-layout class="no-print">
      <v-flex md1>
        <v-select
          :items="refinedWardsForReport"
          v-model="stdReportParameters.ward"
          label="WARD"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-select
          :items="genderValues"
          v-model="stdReportParameters.sex"
          label="GENDER"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-select
          :items="schoolTypes"
          v-model="stdReportParameters.schoolType"
          label="SCHOOL TYPE"
          item-text="name"
          item-value="id"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-select
          :items="subjects"
          v-model="stdReportParameters.subject"
          label="SUBJECT"
          item-text="name"
          item-value="id"
          outline
        ></v-select>
      </v-flex>


      <v-flex offset-md1 md2>
        <v-select
          :items="thType"
          v-model="stdReportParameters.thType"
          label="MARKS TYPE"
          outline
        ></v-select>
      </v-flex>

      <v-flex offset-md1 md2>
        <v-btn @click="downloadStdReport">DOWNLOAD</v-btn>
      </v-flex>

    </v-layout>
  </div>
</template>

<style scoped>
  table th {
    /*font-size: 8px !important;*/
  }
</style>

<script>

import InstallerApi from '../../api/installer'
import AdminApi from '../../api/admin'
import WardChartSchool from '../../components/WardChartSchool'
import WardChartStudent from '../../components/WardChartStudent'
import GradePieChart from '../../components/GradePieChart'
import SchoolTypeApi from '../../api/school-types'
import DownloadApi from '../../api/downloads'
import SubjectApi from '../../api/subjects'

export default {
  components: {
    GradePieChart,
    WardChartStudent,
    WardChartSchool
  },
  created () {
    AdminApi.dashboard().then((res) => {
      this.wardWiseTable = res.body

      this.gradeWiseData = this.setAllWardData()
    })

    SchoolTypeApi.index().then((response) => {
      this.schoolTypes = response.body

      this.schoolTypes.unshift({
        id: null,
        name: 'All'
      })
    })

    SubjectApi.index().then((res) => {
      this.subjects = res.body

      this.subjects.unshift({
        id: null,
        name: 'All'
      })
    })
  },
  // components: [WardChartSchool],
  computed: {

    refinedWards () {
      let wards = this.wardWiseTable.wards.map(i => i.id)
      wards.unshift('All')

      return wards
    },

    refinedWardsForReport () {
      let wards = [{ text: 'All', value: null }]

      this.wardWiseTable.wards.map(i => i.id).forEach(i => {
        wards.push({
          text: i,
          value: i
        })
      })

      return wards
    },

    publicTotalSchoolCount () {
      let total = 0
      for (let ward in this.wardWiseTable.school) total += this.wardWiseTable.school[ward].public_s

      return total
    },

    privateTotalSchoolCount () {
      let total = 0
      for (let ward in this.wardWiseTable.school) total += this.wardWiseTable.school[ward].private_s

      return total
    },

    maleTotalStudentCount () {
      let total = 0
      for (let ward in this.wardWiseTable.student) total += this.wardWiseTable.student[ward].male

      return total
    },

    femaleTotalStudentCount () {
      let total = 0
      for (let ward in this.wardWiseTable.student) total += this.wardWiseTable.student[ward].female

      return total
    },

    totalSchoolCount () {
      let total = 0
      for (let ward in this.wardWiseTable.school) total += this.wardWiseTable.school[ward].total

      return total
    },

    totalStudentCount () {
      let total = 0
      for (let ward in this.wardWiseTable.student) total += this.wardWiseTable.student[ward].total

      return total
    },

    dataForPie () {
      let datasets = []
      let data = this.gradeWiseData
      // console.log(ranges)
      this.ranges.forEach(item => {
        let total = 0;
        ['male', 'female'].forEach(gender => {
          total += data[gender] && data[gender][item] ? data[gender][item] : 0
        })
        datasets.push(total)
      })

      return {
        labels: this.ranges,
        datasets: [ { backgroundColor: [ '#2fc32f', '#b0dc0b', '#eab404', '#de672c', '#ec2e2e', '#d5429b', '#6f52b8', '#1c7cd5', '#56b9f7', '#0ae8eb' ],
          data: datasets } ]
      }
    }
  },
  data () {
    return {
      valid: true,
      tabActive: 0,
      installationData: {
        noOfWards: null
      },
      selectedWardForTable: 'All',
      wardWiseTable: { wards: [], school: {}, student: {} },
      ranges: ['0 - 0.79', '0.80 - 1.19', '1.20 - 1.59', '1.60 - 1.99', '2.00 - 2.39', '2.40 - 2.79', '2.80 - 3.19', '3.20 - 3.59', '3.60 - 3.99', '4', 'NA'],

      reportParameters: {
        ward: null,
        sex: null,
        schoolType: null,
        subject: null,
        thType: null
      },



      stdReportParameters: {
        ward: null,
        sex: null,
        schoolType: null,
        subject: null,
        thType: null
      },

      thType: [
        {
          text: 'All',
          value: null
        },
        {
          text: 'Th Only',
          value: 1
        }
      ],

      genderValues: [
        {
          text: 'All',
          value: null
        },
        {
          text: 'Male',
          value: 1
        },

        {
          text: 'Female',
          value: 2
        }
      ],
      subjects: [],
      schoolTypes: [],
      wardWiseHeaders: [
        {
          text: 'Ward',
          align: 'left',
          sortable: true,
          value: 'id'
        },
        { text: 'School', value: 'school', sortable: false },
        { text: 'Student', value: 'student', sortable: false }
      ],
      gradeWiseHeaders: [
        {
          text: 'Range',
          align: 'left',
          sortable: false,
          value: 'id'
        },
        { text: 'Male', value: 'male', sortable: false },
        { text: 'Female', value: 'female', sortable: false },
        { text: 'Total', value: 'female', sortable: false }
      ],
      gradeWiseData: {}
    }
  },

  methods: {
    install () {
      InstallerApi.install(this.installationData).then((res) => {
        this.$store.commit('core/updateMetaByKey', { key: 'is_installed', value: '1' })
      })
    },

    setAllWardData () {
      let data = { 'male': {}, 'female': {} };
      ['male', 'female'].forEach(gender => {
        this.ranges.forEach(item => {
          data[gender][item] = 0
          this.wardWiseTable.wards.forEach(innerItem => {
            if (this.wardWiseTable.gradeWise[innerItem.id]) {
              data[gender][item] += (this.wardWiseTable.gradeWise[innerItem.id][gender][item] || 0)
            }
          })
        })
      })

      return data
    },

    wardChangeForGradeTable () {
      console.log(this.selectedWardForTable)
      if (this.selectedWardForTable === 'All') {
        this.gradeWiseData = this.setAllWardData()
      } else {
        this.gradeWiseData = this.wardWiseTable.gradeWise[this.selectedWardForTable] || {}
      }
    },

    downloadReport () {
      DownloadApi.downloadDashboardReport(this.reportParameters)
    },

    downloadStdReport () {
      DownloadApi.downloadBestStudentReport(this.stdReportParameters)
    }
  }
}
</script>

<style scoped>

@media print {
  .width-100 {
    width: 100%;
  }
}

</style>



