import Vue from 'vue'

export default {
  loadDataBySlipNumber (slip) {
    return Vue.http.get('v1/admin/marks-entry', { params: { slip } })
  },

  loadDataBySchoolIdAndSubjectId (schoolId, subjectId) {
    return Vue.http.get('v1/admin/marks-entry/school-wise', { params: { schoolId, subjectId } })
  },

  loadDataBySchoolIdAndSubjectIdSupp (schoolId, subjectId) {
    return Vue.http.get('v1/admin/marks-entry/school-wise-supp', { params: { schoolId, subjectId } })
  },

  save (shsRows) {
    return Vue.http.post('v1/admin/marks-entry', shsRows)
  },

  saveSupp (shsRows) {
    return Vue.http.post('v1/admin/marks-entry/supp', shsRows)
  }
}



