<template>

  <v-layout style="background-color: white">
    <v-flex md12>

      <v-card>
        <v-card-title class="headline">Supplementary list</v-card-title>
      </v-card>

      <v-data-table
        :headers="newHeaders"
        :items="students"
        class="elevation-1"
        :rows-per-page-items="[-1]"
        hide-actions
        style="margin: 10px"
      >
        <template slot="items" slot-scope="props">
          <td>{{ props.index + 1 }}</td>
          <td>{{ props.item.symbolNo }}</td>
          <td v-for="subject in subjects">
            <template v-if="props.item.mappedShs[subject.id]">
              {{ props.item.mappedShs[subject.id].calculatedGrade ? props.item.mappedShs[subject.id].calculatedGrade : 'NA'  }} <v-checkbox v-model="props.item.mappedShs[subject.id].hasSupplementary"></v-checkbox>
            </template>
            <template v-else>
                -
            </template>
          </td>
        </template>
      </v-data-table>

      <v-btn @click="saveSupp()" class="float-right">Update List</v-btn>
    </v-flex>
  </v-layout>
</template>

<script>

import StudentApi from '../../../api/students'
import SubjectApi from '../../../api/subjects'

export default {
  created () {
    StudentApi.failList().then((res) => {
      this.students = res.body.map(s => {
        s.mappedShs = {}
        for (let item of s.studentHasSubjectsById) {
          s.mappedShs[item.subjectId] = item
        }

        return s
      })

      SubjectApi.getSubjectsForASchool(this.$auth.user().school.id).then((res) => {
        this.subjects = res.body.sort((a, b) => a.subjectOrder - b.subjectOrder)
      })
    })
  },

  computed: {
    newHeaders () {
      let mapped = []

      for (let item of this.subjects) {
        mapped.push({ text: item.subjectShortcode, value: item.id })
      }

      console.log(mapped)
      return this.existingHeaders.concat(mapped)
    }
  },

  data () {
    return {
      students: [],
      subjects: [],
      existingHeaders: [
        {
          text: 'SN',
          align: 'left',
          sortable: false,
          value: 'SN',
          width: '2%'
        },
        {
          text: 'SYM No',
          align: 'left',
          sortable: true,
          value: 'symbolNumber'
        }
      ]
    }
  },

  methods: {
    saveSupp () {
      let shsList = []
      for (let student of this.students) {
        for (let index in student.mappedShs) {
          shsList.push(student.mappedShs[index])
        }
      }

      StudentApi.updateShsList(shsList).then((res) => {
        alert('Successfully updated')
      })
    }
  }
}
</script>

<style scoped>

</style>



