import StudentIndex from '../pages/school/students/Index'
import StudentCreate from '../pages/school/students/Create'
import StudentEdit from '../pages/school/students/Edit'
import StudentDetail from '../pages/school/students/Detail'
import StudentAdmitCard from '../pages/school/students/AdmitCard'
import PracticalMarksSubjectWise from '../pages/school/practical-marks/SubjectWise'
import PracticalMarksIndex from '../pages/school/practical-marks/MarkList'
import SchoolProfile from '../pages/school/SchoolProfile'
import SuppMarkEntry from '../pages/common/SuppSchoolWiseMarksEntry'
import MarkEntry from '../pages/common/SchoolWiseMarksEntry'

import SuppList from '../pages/school/supp/Listing'

let routes = [
  {
    path: '/students',
    name: 'students.index',
    component: StudentIndex
  },

  {
    path: '/students/create',
    name: 'students.create',
    component: StudentCreate
  },

  {
    path: '/students/:id/edit',
    name: 'students.edit',
    component: StudentEdit
  },

  {
    path: '/students/admit-card',
    name: 'students.admit-card',
    component: StudentAdmitCard
  },

  {
    path: '/practical-marks/subject-wise',
    name: 'practical-marks.subject-wise',
    component: PracticalMarksSubjectWise
  },

  {
    path: '/practical-marks/mark-list',
    name: 'practical-marks.mark-list',
    component: PracticalMarksIndex
  },

  {
    path: '/students/:id',
    name: 'students.detail',
    component: StudentDetail
  },

  {
    path: '/profile',
    name: 'profile',
    component: SchoolProfile
  },

  {
    path: '/marks-entry',
    name: 'marks-entry',
    component: MarkEntry
  },

  {
    path: '/supp-marks-entry',
    name: 'supp-marks-entry',
    component: SuppMarkEntry
  },

  {
    path: '/supp-list',
    name: 'supp-list',
    component: SuppList
  }
]

for (let index in routes) {
  routes[index].name = 'school.' + routes[index].name
  routes[index].meta = { auth: ['ROLE_SCHOOL'], middleware: ['changepassword'] }
}

export default routes



