<script>
import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  name: 'ward-chart-student',
  props: {
    value: {
      type: Object,
      required: true
    }
  },

  computed: {
    requiredData () {
      let maleCount = []
      let femaleCount = []
      let totalCount = []
      this.value.wards.forEach(ward => {
        maleCount.push(this.value.student[ward.id] && this.value.student[ward.id].male ? this.value.student[ward.id].male : 0)
        femaleCount.push(this.value.student[ward.id] && this.value.student[ward.id].female ? this.value.student[ward.id].female : 0)
        totalCount.push(this.value.student[ward.id] && this.value.student[ward.id].total ? this.value.student[ward.id].total : 0)
      })
      return {
        labels: this.value.wards.map(item => item.id),
        datasets: [
          {
            label: 'Male',
            backgroundColor: 'green',
            data: maleCount
          },
          {
            label: 'Female',
            backgroundColor: 'orange',
            data: femaleCount
          },
          {
            label: 'Total',
            backgroundColor: 'yellow',
            data: totalCount
          }
        ]
      }
    }
  },

  mounted () {
    this.renderChart(this.requiredData, {
      responsive: true,
      layout: {
        padding: '20px'
      },
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      },
      title: {
        display: true,
        text: 'Ward wise Student Chart',
        position: 'bottom'
      }
    })
  }
}
</script>



