import WardIndex from '../pages/admin/wards/Index'
import WardCreate from '../pages/admin/wards/Create'
import WardEdit from '../pages/admin/wards/Edit'

import SchoolIndex from '../pages/admin/schools/Index'
import SchoolCreate from '../pages/admin/schools/Create'
import SchoolEdit from '../pages/admin/schools/Edit'

import DisciplineIndex from '../pages/admin/disciplines/Index'
import DisciplineCreate from '../pages/admin/disciplines/Create'
import DisciplineEdit from '../pages/admin/disciplines/Edit'

import SubjectIndex from '../pages/admin/subjects/Index'
import SubjectCreate from '../pages/admin/subjects/Create'
import SubjectEdit from '../pages/admin/subjects/Edit'

import CentreIndex from '../pages/admin/centres/Index'
import CentreCreate from '../pages/admin/centres/Create'
import CentreEdit from '../pages/admin/centres/Edit'

import UserIndex from '../pages/admin/users/Index'
import UserCreate from '../pages/admin/users/Create'
import UserEdit from '../pages/admin/users/ChangePassword'

import AdminDashboard from '../pages/admin/Dashboard'

import AdminSettings from '../pages/admin/settings/Index'
import AdminJobsIndex from '../pages/admin/Jobs'

import AdminMarksEntrySlipWise from '../pages/admin/marks-entry/SlipWise'

import AdminMarkSheetPrint from '../pages/admin/marksheet/MarksheetPrint'
import AdminDownloadsIndex from '../pages/admin/downloads/Index'

import AdminMarkEntrySchoolWise from '../pages/common/SchoolWiseMarksEntry'
import AdminSuppMarkEntrySchoolWise from '../pages/common/SuppSchoolWiseMarksEntry'


import AdminRoutines from '../pages/admin/Routines'

import NoticeIndex from '../pages/admin/notices/Index'
import NoticeCreate from '../pages/admin/notices/Create'
import NoticeEdit from '../pages/admin/notices/Edit'


import Search from '../pages/admin/Search'

let routes = [
  {
    path: 'wards',
    name: 'wards.index',
    component: WardIndex
  },

  {
    path: 'wards/create',
    name: 'wards.create',
    component: WardCreate
  },

  {
    path: 'wards/:id/edit',
    name: 'wards.edit',
    component: WardEdit
  },

  {
    path: 'schools',
    name: 'schools.index',
    component: SchoolIndex
  },

  {
    path: 'schools/create',
    name: 'schools.create',
    component: SchoolCreate
  },

  {
    path: 'schools/:id/edit',
    name: 'schools.edit',
    component: SchoolEdit
  },

  {
    path: 'users',
    name: 'users.index',
    component: UserIndex
  },

  {
    path: 'users/create',
    name: 'users.create',
    component: UserCreate
  },

  {
    path: 'users/:id/edit',
    name: 'users.edit',
    component: UserEdit
  },

  {
    path: 'disciplines',
    name: 'disciplines.index',
    component: DisciplineIndex
  },

  {
    path: 'disciplines/create',
    name: 'disciplines.create',
    component: DisciplineCreate
  },

  {
    path: 'disciplines/:id/edit',
    name: 'disciplines.edit',
    component: DisciplineEdit
  },

  {
    path: 'subjects',
    name: 'subjects.index',
    component: SubjectIndex
  },

  {
    path: 'subjects/create',
    name: 'subjects.create',
    component: SubjectCreate
  },

  {
    path: 'subjects/:id/edit',
    name: 'subjects.edit',
    component: SubjectEdit
  },

  {
    path: 'centres',
    name: 'centres.index',
    component: CentreIndex
  },

  {
    path: 'centres/create',
    name: 'centres.create',
    component: CentreCreate
  },

  {
    path: 'centres/:id/edit',
    name: 'centres.edit',
    component: CentreEdit
  },

  {
    path: 'dashboard',
    name: 'dashboard',
    component: AdminDashboard
  },

  {
    path: 'settings',
    name: 'settings',
    component: AdminSettings
  },

  {
    path: 'jobs',
    name: 'jobs',
    component: AdminJobsIndex
  },

  {
    path: 'marks-entry/slip-wise',
    name: 'marks-entry.slip-wise',
    component: AdminMarksEntrySlipWise
  },

  {
    path: 'marksheet/print',
    name: 'marksheet.print',
    component: AdminMarkSheetPrint
  },

  {
    path: 'downloads',
    name: 'downloads.index',
    component: AdminDownloadsIndex
  },

  {
    path: 'marks-entry/school-wise',
    name: 'marks-entry.school-wise',
    component: AdminMarkEntrySchoolWise
  },

  {
    path: 'supp-marks-entry/school-wise',
    name: 'supp-marks-entry.school-wise',
    component: AdminSuppMarkEntrySchoolWise
  },

  {
    path: 'routines',
    name: 'routines.index',
    component: AdminRoutines
  },

  {
    path: 'notices',
    name: 'notices.index',
    component: NoticeIndex
  },

  {
    path: 'notices/create',
    name: 'notices.create',
    component: NoticeCreate
  },

  {
    path: 'notices/:id/edit',
    name: 'notices.edit',
    component: NoticeEdit
  },

  {
    path: 'search',
    name: 'search',
    component: Search
  }
]

for (let index in routes) {
  routes[index].path = '/admin/' + routes[index].path
  routes[index].name = 'admin.' + routes[index].name
  routes[index].meta = { auth: ['ROLE_ADMIN'], middleware: ['changepassword'] }
}

export default routes



