<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Notices</h2>
      </v-flex>

      <v-flex justify-end>
        <v-btn fab :to="{ name: 'admin.notices.create' }" class="float-right"><v-icon>add</v-icon></v-btn>
      </v-flex>
    </v-layout>


    <v-data-table
      :headers="headers"
      :items="items"
      class="elevation-1"
      :rows-per-page-items="[-1]"
      hide-actions
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ props.item.id }}</td>
        <td>{{ props.item.title }}</td>
        <td>{{ props.item.publishedDate }}</td>
        <td><v-btn danger flat @click=" deleteNotice(props.item) "><v-icon>delete</v-icon></v-btn><v-btn :to="{ name: 'admin.notices.edit', params: { id: props.item.id } }" primary flat><v-icon>create</v-icon></v-btn></td>
      </template>
    </v-data-table>

  </div>

</template>

<script>

  import NoticeApi from '../../../api/notices'
export default {
  created () {
    NoticeApi.index().then((response) => {
      this.items = response.body
    })
  },

  data () {
    return {
      search: '',
      headers: [
        {
          text: '#',
          align: 'left',
          sortable: true,
          value: 'id'
        },
        { text: 'Title', value: 'title' },
        { text: 'Published Date', value: 'publishedDate' },
        { text: 'Actions',value: 'actions', width: '20%' }
      ],
      items: []
    }
  },

  methods: {
    deleteNotice (notice) {
      NoticeApi.delete(notice.id).then((response) => {
        let index = this.items.indexOf(notice)
        this.items.splice(index, 1)
      })
    }
  }
}
</script>

<style scoped>

</style>



