<template>
  <v-form v-model="valid" ref="subject_form">
    <v-layout>
      <v-flex md4>

        <v-text-field
          v-model="subject.id"
          :counter="6"
          label="Subject Code"
          required
          :rules="codeRules"
        ></v-text-field>

        <v-text-field
          v-model="subject.name"
          :rules="nameRules"
          :counter="100"
          label="Subject Name"
          required
          class="my-text-uppercase"
        ></v-text-field>

        <v-checkbox
          v-model="subject.hasPractical"
          label="Has Practical"
        ></v-checkbox>

        <v-text-field
          v-model="subject.fullMarks"
          :counter="3"
          label="TH Full Marks"
          :rules="fullMarksRules"
          required
          type="number"
        ></v-text-field>

        <v-text-field
          v-model="subject.passMarks"
          :counter="2"
          label="TH Pass Marks"
          :rules="passMarksRules"
          required
          type="number"
        ></v-text-field>

        <v-text-field
          v-model="subject.creditHour"
          :counter="1"
          label="Credit Hour"
          :rules="creditHourRules"
          required
          type="number"
        ></v-text-field>

        <v-btn
          :disabled="!valid"
          @click="submit"
        >
          submit
        </v-btn>
        <v-btn @click="clear">clear</v-btn>

      </v-flex>

      <v-flex md4 offset-md1>
        <v-text-field
          v-model="subject.subjectShortcode"
          :counter="6"
          label="Short Name"
          :rules="nameRules"
          required
          class="my-text-uppercase"
        ></v-text-field>

        <v-text-field
          v-model="subject.subjectOrder"
          :counter="2"
          label="Subject Order"
          required
          type="number"
        ></v-text-field>

        <div v-show="subject.hasPractical">
          <v-text-field
            v-model="subject.prFullMarks"
            :counter="2"
            label="Practical Full Marks"
            :rules="prFullMarksRules"
            required
            type="number"
          ></v-text-field>

          <v-text-field
            v-model="subject.prPassMarks"
            :counter="2"
            label="Practical Pass Marks"
            required
            type="number"
            :rules="prPassMarksRules"
          ></v-text-field>
        </div>
      </v-flex>
    </v-layout>

  </v-form>

</template>

<script>
import SubjectApi from '../../../api/subjects'

export default {
  name: 'subject-form',

  created () {
    this.oldId = this.$route.params.id
  },

  watch: {
    'subject.hasPractical': {
      handler (final, old) {
        if (old !== undefined) { this.$refs.subject_form.validate() }
      },
      deep: true,
      immediate: true
    }

  },

  props: {
    subject: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      oldId: null,
      valid: false,
      name: '',
      id: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 100) || 'Name must be less than 100 characters',
        v => (v && (/^[A-Za-z .]+$/g).test(v)) || 'Please provide valid name'
      ],

      codeRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 6) || 'Name must be less than 6 characters',
        v => (v && (/^[A-Za-z0-9]+$/gm).test(v)) || 'Please provide valid name'
      ],

      fullMarksRules: [
        v => !!v || 'Full marks is required',
        v => (v && v <= 100) || 'Full marks must be less than 100'
      ],

      passMarksRules: [
        v => !!v || 'Pass marks is required',
        v => (v && v < parseInt(this.subject.fullMarks)) || 'Pass marks must be less than full marks'
      ],

      creditHourRules: [
        v => !!v || 'Credit Hour is required',
      ],

      prFullMarksRules: [
        v => !this.subject.hasPractical || !!v || 'Practical Full marks is required',
        v => this.subject.hasPractical.toString() === 'false' || (v && v <= parseInt(this.subject.fullMarks)) || 'Practical Full marks must be less than ' + this.subject.fullMarks
      ],

      prPassMarksRules: [
        v => this.subject.hasPractical.toString() === 'false' || !!v || 'Practical Pass marks is required',
        v => this.subject.hasPractical.toString() === 'false' || (v && v < parseInt(this.subject.prFullMarks)) || 'Pass marks must be less than full marks'
      ]

    }
  },

  methods: {
    clear () {
      this.$refs.subject_form.reset()
    },

    submit () {
      SubjectApi.save(this.subject, this.oldId).then((response) => {
        this.$router.push({ name: 'admin.subjects.index' })
      })
    }
  }
}
</script>

<style scoped>

</style>



