<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Add New Centre</h2>
      </v-flex>
      <v-flex md1>
        <v-btn fab :to="{ name: 'admin.centres.index' }" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <centre-form :centre="blankCentre"></centre-form>

  </div>

</template>

<script>

import CentreForm from './Form'

export default {
  components: { CentreForm },
  created () {
  },

  data () {
    return {
      blankCentre: {
        id: null,
        schoolId: '',
        centreCode: '',
        schoolList: [],
        centreHasSchoolsById: [{
          schoolId: null,
          centreId: null,
          centreHasSchoolList: []
        }]
      }
    }
  }
}
</script>

<style scoped>

</style>



