<template>
  <v-container style="max-height: 140px !important;" fluid class="no-print">
    <v-layout row wrap>

      <v-flex md1>
        <router-link :to="{ name: $auth.check('ROLE_ADMIN') ? 'admin.dashboard' : 'dashboard' }"><v-img height="95px" :contain="true" src="/static/logo_1.png"></v-img></router-link>
      </v-flex>


      <v-flex md3 class="logo-text">
        <router-link class="my-link" :to="{ name: $auth.check('ROLE_ADMIN') ? 'admin.dashboard' : 'dashboard' }"><p>{{ $store.state.core.metas.municipal_name }}</p></router-link>
        <p class="logo-text-small">{{ $store.state.core.metas.municipal_office_name }}</p>
      </v-flex>

      <v-flex m2 text-xs-right v-if="$auth.check()">
        <p class="subheading">Hello, {{ $auth.user().username }}! <template v-if="$auth.user().school"><br>School Name: {{ $auth.user().school.name }}</template></p>
        <v-layout row>
          <v-flex m1 text-xs-right>
            <v-btn flat color="black" outline v-if="$auth.impersonating()" v-on:click="$auth.unimpersonate()">Back to Admin
              Panel
            </v-btn>
            <v-btn flat color="black" outline :to="{ name: 'auth.change-password' }">Change Password</v-btn>
            <v-btn flat color="black" outline @click="logout">Logout</v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'top-header',
  methods: {
    logout () {
      this.$auth.logout()
      this.$bus.$emit('menu.change')
    }
  }
}
</script>

<style scoped>
  .logo-text {
    font-size: 20px;
    float: left;
    margin-top: 1%;
    line-height: 0.6;
  }

  .logo-text-small {
    font-style: italic;
    font-size: 10px;
  }

  .my-link {
    text-decoration: none;
    color: black;
  }

  .my-link:hover
  {
    color:black;
    text-decoration:none;
    cursor:pointer;
  }
</style>



