<template>
  <div>
    <v-layout>
      <h2>Search Student</h2>
    </v-layout>
    <v-layout style="margin-top: 20px">
      <v-flex md2>
        <v-text-field
          v-model="search"
          label="Reg No or Symbol Number"
          @keyup=""
        >
        </v-text-field>
      </v-flex>

      <v-flex md1>
        <v-btn @click="searchStudent">Search</v-btn>
      </v-flex>
    </v-layout>

    <v-layout md2 v-show=" Object.keys(student).length === 0 && notFound " style="margin-top: 10px">
      <h3>Record not found</h3>
    </v-layout>

    <student-show :student="student" v-show=" Object.keys(student).length !== 0 "></student-show>
  </div>
</template>

<script>
import StudentShow from '../school/students/Show'
import StudentApi from '../../api/students'
export default {
  components: {
    StudentShow
  },
  data: () => {
    return {
      search: null,
      student: {},
      notFound: false
    }
  },
  methods: {
    searchStudent () {
      StudentApi.searchStudent(this.search).then((res) => {
        if (res.body) { this.student = res.body } else { this.notFound = true }
      })
    },

    searchKeyup () {
      this.notFound = false
      this.student = {}
    }
  }
}
</script>

<style scoped>

</style>



