<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Add New Subject</h2>
      </v-flex>
      <v-flex md2>
        <v-btn fab :to="{ name: 'admin.subjects.index' }"  class="float-right" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <subject-form :subject="blankSubject"></subject-form>

  </div>
</template>

<script>

import SubjectForm from './Form'

export default {

  components: { SubjectForm },
  created () {
  },

  data () {
    return {
      hasPractical: null,
      blankSubject: {
        id: null,
        name: '',
        hasPractical: false,
        fullMarks: null
      }
    }
  }
}
</script>

<style scoped>

</style>



