<template>
  <div>
    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit Ward</h2>
      </v-flex>
      <v-flex md2>
        <v-btn fab :to="{ name: 'admin.wards.index' }"  class="float-right" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <ward-form :ward="ward"></ward-form>
  </div>
</template>

<script>
import WardForm from './Form'
import WardApi from '../../../api/wards'

export default {
  components: { WardForm },

  data () {
    return {
      ward: {
        id: '',
        name: ''
      }
    }
  },

  watch: {
    '$route'(to, from) {
      this.created()
    }
  },

  created () {
    WardApi.findById(this.$route.params.id).then((response) => {
      this.ward = response.body
    })
  }
}
</script>



