<template>
  <div>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Practical Marks Entry</h2>
        <v-btn :to="{ name: 'school.practical-marks.mark-list' }">View Entered Marks</v-btn>
      </v-flex>
    </v-layout>

    <v-form v-modal="valid">
      <v-layout row wrap mb40>
        <v-flex md5>
          <v-select
            v-model="query.subjects"
            :items="subjects"
            label="Subjects"
            single-line
            item-text="subjectShortcode"
            item-value="id"
            multiple
          ></v-select>
        </v-flex>
        <v-flex offset-md1 md1>
          <v-btn @click="getDataFromApi">Load Data</v-btn>
        </v-flex>

      </v-layout>

      <v-data-table
        :headers="headers"
        :items="students"
        class="elevation-1"
        :search="search"
        :rows-per-page-items="[10,20,50]"
        :pagination.sync="pagination"
        :total-items="totalElements"
      >
        <template slot="headerCell" slot-scope="props">
          <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
            <span>
          {{ props.header.text }}
        </span>
          </v-tooltip>
        </template>
        <template slot="items" slot-scope="props">
          <td>{{ props.item.id }}</td>
          <td>{{ props.item.symbolNo }}</td>
          <td>{{ props.item.fullName }}</td>
          <td v-for="(subject, index) in subjectHeader" v-if="props.item.studentHasSubjectsById[subject.value]">
            <v-text-field
              :error="props.item.studentHasSubjectsById[subject.value].error"
              v-model="props.item.studentHasSubjectsById[subject.value].prMarks"
              :append-icon=" props.item.studentHasSubjectsById[subject.value].prMarks ? (props.item.studentHasSubjectsById[subject.value].error ? 'close' : 'check') : undefined "
              :ref="'marks-' + props.index + '-' + index"
              @keyup="marksEntered(props.item.studentHasSubjectsById[subject.value], props.index, index)"
            ></v-text-field>
          </td>
          <td v-else>
            NA
          </td>

        </template>

        <template slot="actions-prepend">
          <v-btn color="primary" @click="saveMarks">{{ hasNextPage ? 'Save and Next' : 'Save' }}</v-btn>
        </template>
      </v-data-table>

    </v-form>

  </div>
</template>

<script>
import SubjectApi from '../../../api/subjects'
import PracticalMarksApi from '../../../api/practical-marks'
import Util from '../../../utils'

export default {
  created () {
    if (Util.dateWiseLock(this.$store.state.core.metas.practical_entry_lock_date) && !this.$auth.impersonating()) {
      alert('Practical Marks Entry is locked by admin. Please contact administrator.')
      this.$router.push({
        name: 'school.practical-marks.mark-list'
      })
    }

    SubjectApi.getPracticalSubjectsForCurrentlyLoggedUser().then((res) => {
      this.subjects = res.body

      this.subjects.forEach((item) => {
        this.mappedSubjects[item.id] = item
      })

      this.generateHeader()
    })
  },

  watch: {
    pagination: {
      handler () {
        this.getDataFromApi()
      },
      deep: true
    },

    subjectHeader: {
      handler () {
        // console.log('Over here', this.partialHeaders, this.subjectHeader)
        this.headers = this.partialHeaders.concat(this.subjectHeader)
      },
      deep: true
    },

    query: {
      handler () {
        // console.log('Subject changed')
        // this.generateHeader()
      },
      deep: true
    }
  },

  data () {
    return {
      orderBy: [
        {
          key: 'Registration Number',
          value: 'id'
        }
      ],
      query: {
        orderBy: 'id',
        perPage: 10,
        subjects: []
      },
      subjectRules: (subject) => {
        let aSubject = this.subjects.filter((item) => item.id === subject.subjectId)
        console.log(aSubject, this.subjects)
        if (aSubject.length > 0) {
          return [
            v => !v || parseInt(v) <= parseInt(aSubject[0].prFullMarks) || 'Marks should be less than full marks.'
          ]
        }

        return [
          v => true
        ]
      },
      hasNextPage: false,
      startingRegNumber: '',
      headers: [],
      subjectHeader: [],
      partialHeaders: [
        {
          text: 'Registration Number',
          align: 'left',
          sortable: false,
          value: 'id',
          width: '4%'
        },
        {
          text: 'Symbol Number',
          align: 'left',
          sortable: false,
          value: 'symbolNo',
          width: '3%'
        },
        {
          text: 'Name',
          align: 'left',
          sortable: false,
          value: 'symbolNo',
          width: '7%'
        }
      ],
      students: [],
      subjects: [],
      search: null,
      pagination: {},
      mappedSubjects: {},
      totalElements: null,
      valid: false,
      errorMessages: [
        'Please provide marks less than full marks'
      ]
    }
  },

  methods: {
    generateHeader () {
      let subjectHeader = []
      if (this.query.subjects.length === 0) {
        this.subjects.forEach((subject) => {
          subjectHeader.push({
            text: subject.subjectShortcode,
            value: subject.id,
            sortable: false,
            width: '2%'
          })
        })
      } else {
        this.query.subjects.forEach((subject) => {
          subjectHeader.push({
            text: this.mappedSubjects[subject].subjectShortcode,
            value: this.mappedSubjects[subject].id,
            sortable: false,
            width: '4%'
          })
        })
      }

      this.subjectHeader = subjectHeader
    },

    getDataFromApi () {
      this.query.page = this.pagination.page - 1
      this.query.perPage = this.pagination.rowsPerPage
      PracticalMarksApi.loadDataForPracticalMarksEntry(this.query).then((res) => {
        this.students = this.mapData(res.body.content)
        this.totalElements = res.body.totalElements
        this.pagination.page = res.body.number + 1
        this.hasNextPage = !res.body.last
        this.generateHeader()
      })
    },

    saveMarks () {
      let valid = this.students.filter(item => {
        let errorCount = 0
        for (let objKey in item.studentHasSubjectsById) {
          if (item.studentHasSubjectsById[objKey].error) { errorCount++ }
        }
        return errorCount !== 0
      }).length === 0

      if (valid) {
        PracticalMarksApi.savePracticalMarks(this.mapForSavingData(this.students)).then((res) => {
          if (this.hasNextPage) {
            this.pagination.page++
          } else {
            alert('Practical marks entry complete for selected students.')
          }
        })
      } else {
        alert('There are some invalid marks.')
      }
    },

    mapData (data) {
      return data.map((student) => {
        let subjects = {}
        student.studentHasSubjectsById.forEach((subject) => {
          subject.error = false
          subjects[subject.subjectId] = subject
        })

        student.studentHasSubjectsById = subjects

        return student
      })
    },

    mapForSavingData (data) {
      let rows = []

      data.forEach((student) => {
        rows = rows.concat(Object.values(student.studentHasSubjectsById))
      })

      return rows
    },

    marksEntered (shs, verticalIndex, horizontalIndex) {
      console.log(shs)
      let marks = shs.prMarks
      if (shs.prMarks && (parseInt(shs.prMarks) > this.mappedSubjects[shs.subjectId].prFullMarks)) {
        shs.error = true
        return
      } else {
        shs.error = false
      }

      if (marks && marks.toString().length >= 2) {
        this.goTo('marks-' + verticalIndex + '-' + (parseInt(horizontalIndex) + 1))
      }
    }
  }
}
</script>

<style scoped>

</style>



