<script>
import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  name: 'ward-chart-school',
  props: {
    value: {
      type: Object,
      required: true
    }
  },

  computed: {
    requiredData () {
      let publicSchoolCount = []
      let privateSchoolCount = []
      let totalSchoolCount = []
      console.log(this.value)
      this.value.wards.forEach(ward => {
        publicSchoolCount.push(this.value.school[ward.id] && this.value.school[ward.id].public_s ? this.value.school[ward.id].public_s : 0)
        privateSchoolCount.push(this.value.school[ward.id] && this.value.school[ward.id].private_s ? this.value.school[ward.id].private_s : 0)
        totalSchoolCount.push(this.value.school[ward.id] && this.value.school[ward.id].total ? this.value.school[ward.id].total : 0)
      })
      return {
        labels: this.value.wards.map(item => item.id),
        datasets: [
          {
            label: 'Public School',
            backgroundColor: 'green',
            data: publicSchoolCount
          },
          {
            label: 'Private School',
            backgroundColor: 'orange',
            data: privateSchoolCount
          },
          {
            label: 'Total',
            backgroundColor: 'yellow',
            data: totalSchoolCount
          }
        ]
      }
    }
  },

  mounted () {
    this.renderChart(this.requiredData, {
      responsive: true,
      layout: {
        padding: '20px'
      },
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      },
      title: {
        display: true,
        text: 'Ward wise School Chart',
        position: 'bottom'
      }
    })
  }
}
</script>



