<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit Student</h2>
      </v-flex>
      <v-flex md1>
        <v-btn fab :to="{ name: 'school.students.index' }" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <student-form :student="student"></student-form>

  </div>

</template>

<script>
import StudentForm from './Form'
import StudentApi from '../../../api/students'
import StudentTransformer from '../../../transformers/students'

export default {
  components: { StudentForm },
  watch: {
    '$route' (to, from) {
      this.created()
    }
  },

  created () {
    StudentApi.findById(this.$route.params.id).then((response) => {
      this.student = StudentTransformer.vue(response.body)
    })
  },

  data () {
    return {
      student: {},
      seletedDiscipline: {}
    }
  }

}
</script>

<style scoped>

</style>



