export default {
  tansformDisciplineHasSubject(disciplineHasSubject) {
    let newDisciplineHasSubject = []
    for (let key in disciplineHasSubject) {
      disciplineHasSubject[key].forEach((item) => {
        newDisciplineHasSubject.push({
          id: null,
          disciplineId: null,
          subjectId: item.id,
          subjectOrder: key
        })
      })
    }

    return newDisciplineHasSubject
  },

  tansformDisciplineHasSubjectVue(disciplineHasSubject) {
    let newDisciplineHasSubject = {}
    console.log(disciplineHasSubject)

    if (disciplineHasSubject) {
      disciplineHasSubject.forEach((item) => {
        if (newDisciplineHasSubject[item.subjectOrder]) {
          newDisciplineHasSubject[item.subjectOrder].push(item.subjectsBySubjectId)
        } else {
          newDisciplineHasSubject[item.subjectOrder] = [item.subjectsBySubjectId]
        }
      })
    }

    return newDisciplineHasSubject
  }

}



