<template>
  <v-app>
    <div v-if="$auth.ready()">
      <top-header></top-header>
      <nav-bar></nav-bar>
      <v-content>
        <v-container fluid>

          <router-view></router-view>

        </v-container>
      </v-content>
      <v-footer app></v-footer>
    </div>
    <div v-else>
      Site loading...
    </div>
  </v-app>
</template>

<script>

import NavBar from './components/NavBar'
import TopHeader from './components/TopHeader'
import  MetaApi from  './api/metas'
import Bus from './Bus'

export default {
  components: {
    TopHeader,
    NavBar
  },
  name: 'app',
  created () {
    // console.log(this.$router.options.routes)
    this.$bus.$on('error.on.refresh', (err) => {
      this.$auth.logout()
      this.$router.push({ name: 'auth.login' })
    })

    Bus.$on('load.metas', () => {
      MetaApi.index().then((res) => {
        this.$store.commit('core/mapAndStoreAllMetas', res.body)
        Bus.$emit('menu.change')
        this.$gtm.trackEvent({
          event: null, // Event type [default = 'interaction'] (Optional)
          category: 'Site Loaded',
          action: 'loading',
          label: 'Municipal',
          value: this.$store.state.core.metas.municipal_name,
          noninteraction: false // Optional
        });
      })
    })
  },
  mounted () {
    Bus.$emit('load.metas')
  }
}
</script>
<style>
  th, td {
    border: 1px solid black;
    border-collapse:collapse;
  }

  th :last-child {
    border: unset;
  }

  th {
    background-color: #E8E8E8;
    color: black !important;
    font-size: 14px !important;
    font-weight: bold !important;
  }
</style>



