import Vue from 'vue'

export default {
  runJob (name, params = {}) {

    params['date'] = new Date().toISOString()
    let paramStr = []
    for (let key in params)
      paramStr.push( key + '=' + params[key] )

    return Vue.http.post('batch/operations/jobs/' + name, {}, {
      params: {
        jobParameters: paramStr.join(',')
      }
    })
  },
  getJobInfo () {
    return Vue.http.get('batch/monitoring/job-info')
  },
}



