<template>
  <div>
    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit Notice</h2>
      </v-flex>
      <v-flex md2>
        <v-btn fab :to="{ name: 'admin.notices.index' }"  class="float-right" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <notice-form :notice="notice"></notice-form>
  </div>
</template>

<script>
import NoticeForm from './Form'
import NoticeApi from '../../../api/notices'

export default {
  components: { NoticeForm },

  data () {
    return {
      notice: {
        id: '',
        title: '',
        content: '',
        publishedDate: ''
      }
    }
  },

  watch: {
    '$route'(to, from) {
      this.created()
    }
  },

  created () {
    NoticeApi.findById(this.$route.params.id).then((response) => {
      this.notice = response.body
    })
  }
}
</script>



