<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Wards</h2>
      </v-flex>

      <v-flex md4>
        <v-text-field
          v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-flex>
      <v-flex justify-end>
        <v-btn fab :to="{ name: 'admin.wards.create' }" class="float-right"><v-icon>add</v-icon></v-btn>
      </v-flex>
    </v-layout>


    <v-data-table
      :headers="headers"
      :items="items"
      class="elevation-1"
      :rows-per-page-items="[-1]"
      :search="search"
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ props.item.id }}</td>
        <td>{{ props.item.name }}</td>
        <td><v-btn danger flat @click=" deleteWard(props.item) "><v-icon>delete</v-icon></v-btn><v-btn :to="{ name: 'admin.wards.edit', params: { id: props.item.id } }" primary flat><v-icon>create</v-icon></v-btn></td>
      </template>
    </v-data-table>

  </div>

</template>

<script>

  import WardApi from '../../../api/wards'
export default {
  created () {
    WardApi.index().then((response) => {
      this.items = response.body
    })
  },

  data () {
    return {
      search: '',
      headers: [
        {
          text: 'Ward No',
          align: 'left',
          sortable: true,
          value: 'id'
        },
        { text: 'Name', value: 'name' },
        { text: 'Actions',value: 'actions', width: '20%' }
      ],
      items: []
    }
  },

  methods: {
    deleteWard (ward) {
      WardApi.delete(ward.id).then((response) => {
        let index = this.items.indexOf(ward)
        this.items.splice(index, 1)
      })
    }
  }
}
</script>

<style scoped>

</style>



