export default {

  methods: {
    goTo (ref) {
      this.$nextTick(() => {
        console.log('HELLO 1')

        if (this.$refs[ref]) {
          if (Array.isArray(this.$refs[ref])) {
            this.$refs[ref][0].focus()
          } else {
            this.$refs[ref].focus()
          }
        }
      })
    }
  }
}



