<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Add New Student</h2>
      </v-flex>
      <v-flex md1>
        <v-btn fab :to="{ name: 'school.students.index' }" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <student-form :student="blankStudent"></student-form>

  </div>

</template>

<script>

import StudentForm from './Form'

export default {
  components: { StudentForm },
  created () {
  },

  data () {
    return {
      blankStudent: {
        id: null,
        fName: '',
        mName: '',
        lName: '',
        year: '',
        schoolId: '',
        sex: '',
        fatherName: '',
        dob: '',
        dobYear: '',
        dobMonth: '',
        dobDay: '',
        disciplineId: '',
        symbolNo: '',
        studentHasSubjectsById: []
      }
    }
  }
}
</script>

<style scoped>

</style>



