<template>

  <v-form v-model="valid" ref="student_form">
    <v-layout>
      <v-flex md5>
        <h3>Student Data</h3>

        <v-text-field
          v-model="student.fName"
          :counter="30"
          label="First Name"
          required
          class="my-text-uppercase"
          :rules="nameRules"
        ></v-text-field>

        <v-text-field
          v-model="student.mName"
          :counter="30"
          label="Middle Name"
          class="my-text-uppercase"
          :rules="middleNameRules"
        ></v-text-field>

        <v-text-field
          v-model="student.lName"
          :counter="30"
          label="Last Name"
          required
          class="my-text-uppercase"
          :rules="lNameRules"
        ></v-text-field>

        <v-select
          v-model="student.sex"
          required
          :items="sexData"
          label="Sex"
          item-text="name"

          :rules="sexRules"
        >

        </v-select>

        <v-text-field
          v-model="student.fatherName"
          :counter="50"
          label="Father Name"
          required
          class="my-text-uppercase"
        ></v-text-field>

        <v-text-field
          v-model="student.motherName"
          :counter="50"
          label="Mother Name"
          required
          class="my-text-uppercase"
        ></v-text-field>

        <v-layout>
          <v-flex md2>
            <v-text-field
              v-model="student.dobYear"
              label="YYYY"
              required
              type="number"
              append-outer-icon="dash"
              :rules="yearRules"
            ></v-text-field>
          </v-flex>

          <v-flex md2><v-text-field
            v-model="student.dobMonth"
            label="MM"
            required
            type="number"
            :rules="monthRules"
            append-outer-icon="dash"
          ></v-text-field></v-flex>

          <v-flex md1><v-text-field
            v-model="student.dobDay"
            label="DD"
            required
            :rules="dayRules"
            type="number"
          ></v-text-field></v-flex>

        </v-layout>

        <v-select
          v-model="student.disciplineId"
          :items="disciplines"
          attach
          standard
          label="Disciplines"
          item-text="name"
          item-value="id"
          required
          :rules="disciplineRules"
          @change="disciplineChangeHandler(true)"
        ></v-select>

        <v-text-field
          v-model="student.symbolNo"
          :counter="7"
          label="Symbol Number"
          :disabled="$store.state.core.metas.manual_symbol_number !== 'true'"
          v-show="$route.params.id || $store.state.core.metas.manual_symbol_number === 'true'"
          v-validate="$store.state.core.metas.manual_symbol_number === 'true' ? 'required|symbol_number' + (student.id ? ':' + student.id : '') : ''"
          :error-messages="errors.collect('symbolNumber')"
          :error="errors.has('symbolNumber')"
          data-vv-name="symbolNumber"
        ></v-text-field>

        <v-text-field
          v-model="student.id"
          :counter="10"
          label="Reg Number"
          disabled
          v-show="$route.params.id"
        ></v-text-field>

        <v-btn
          :disabled="!valid && !submitingForm"
          @click="submit()"

        >
          {{ submitingForm ? 'Uploading' : 'Submit' }}
        </v-btn>

        <v-btn
          :disabled="!valid && !submitingForm"
          @click="submit(true)"

        >
          {{ submitingForm ? 'Uploading' : 'Submit And Add Another' }}
        </v-btn>

        <v-btn @click="clear">clear</v-btn>
      </v-flex>

      <v-flex md5 offset-md1>
        <h3>Subjects</h3>

        <div v-if="selectedDiscipline">

          <div v-for=" subjectRow in student.studentHasSubjectsById ">

            <v-select
              v-model="subjectRow.subjectId"
              :items="selectedDiscipline.mappedSubjects[subjectRow.subjectOrder]"
              attach
              standard
              :label="'Subject Order ' +  subjectRow.subjectOrder"
              item-text="subjectName"
              item-value="subjectId"
              required
            ></v-select>
          </div>
        </div>

      </v-flex>
    </v-layout>
  </v-form>
</template>

<script>
import WardApi from '../../../api/wards'
import SchoolApi from '../../../api/schools'
import Util from '../../../utils'
import StudentApi from '../../../api/students'
import StudentTransformer from '../../../transformers/students'

export default {
  name: 'student-form',

  created () {
    if (Util.dateWiseLock(this.$store.state.core.metas.student_entry_lock_date) && !this.$auth.impersonating()) {
      alert('Student entry and edit is locked by admin. Please contact administrator.')
      this.$router.push({
        name: 'school.students.index'
      })
    }
    SchoolApi.getDisciplinesForLoggedUserSchool().then((response) => {
      this.disciplines = response.body

      this.refinedDisciplines = {}

      // For selecting default school
      if (!this.$route.params.id && this.disciplines.length === 1) this.student.disciplineId = this.disciplines[0].id

      this.disciplines.forEach((item) => {
        this.refinedDisciplines[item.id] = item

        item.disciplineHasSubjectsById.forEach((s) => s.subjectName = s.subjectsBySubjectId.name)
        this.refinedDisciplines[item.id].mappedSubjects = Util.groupBy(item.disciplineHasSubjectsById, 'subjectOrder')
      })

      this.disciplineChangeHandler()

      if (!this.$route.params.id && this.$store.state.core.metas.manual_symbol_number === 'true' && this.$store.state.core.symbolNumber) {
        // StudentApi.generateNewSymbolNo(this.student.disciplineId).then(res => {
        //   this.student.symbolNo = res.body.symbolNo
        // })

        this.student.symbolNo = this.$store.state.core.symbolNumber
      }
    })
  },

  props: {
    student: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      disciplines: [],
      sexData: [
        {
          name: 'Female',
          value: 2
        },

        {
          name: 'Male',
          value: 1
        }
      ],
      wards: [],
      valid: false,
      name: '',
      id: '',
      submitingForm: false,
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 50) || 'Name must be less than 50 characters',
        v => (v && (/^[A-Za-z ']+$/g).test(v)) || 'Please provide valid name'
      ],

      disciplineRules: [
        v => !!v || 'Discipline must be selected'
      ],

      lNameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 50) || 'Name must be less than 50 characters',
        v => (v && (/^[A-Za-z .']+$/g).test(v)) || 'Please provide valid name'
      ],

      middleNameRules: [
        v => !v || (v && v.length <= 50) || 'Name must be less than 50 characters',
        v => !v || (v && (/^[A-Za-z']+$/g).test(v)) || 'Please provide valid name'
      ],
      parentNameRules: [
        v => !!this.student.fatherName || !!this.student.motherName || 'Name is required',
        v => (v && v.length <= 50) || 'Name must be less than 50 characters',
        v => (v && (/^[A-Za-z .']+$/g).test(v)) || 'Please provide valid name'
      ],
      yearRules: [
        v => !!v || 'Year is required',
        v => (v && parseInt(this.student.dobYear) <= 2070) || 'Year must be less than 2070',
        v => (v && v.toString().length === 4 && (/^[0-9]+$/g).test(v)) || 'Please provide valid month'
      ],

      monthRules: [
        v => !!v || 'Month is required',
        v => (v && parseInt(this.student.dobMonth) <= 12 && parseInt(this.student.dobMonth) >= 1) || 'Month must be between 1 to 12',
        v => (v && (/^[0-9]+$/g).test(v)) || 'Please provide valid month'
      ],

      sexRules: [
        v => !!v || 'Sex is required'
      ],

      dobRules: [
        v => !!v || 'Invalid BS date selected'
      ],

      dayRules: [
        v => !!v || 'Day is required',
        v => (v && parseInt(this.student.dobDay) <= 32 && parseInt(this.student.dobMonth) >= 1) || 'Day must be between 1 to 32',
        v => (v && (/^[0-9]+$/g).test(v)) || 'Please provide valid day'
      ],
      refinedDisciplines: {},
      selectedDiscipline: null,
      originalShsArr: null
    }
  },

  methods: {
    clear () {
      this.$refs.student_form.reset()
    },

    submit (next = false) {
      console.log(next, 'hehe')
      this.submitingForm = true
      StudentApi.save(StudentTransformer.spring(this.student)).then((response) => {
        if (!this.$route.params.id) {
          this.$store.commit('core/updateNextEntrySymbolNumber', this.student.symbolNo)
        }

        this.$router.push({ name: 'school.students.index' })
        if (next) {
          setTimeout(() => this.$router.push({ name: 'school.students.create', query: { reload: new Date().valueOf() } }), 100)
        } else {
        }
      }).catch((err) => {
        alert(err)
        this.submitingForm = false
      })
    },

    disciplineChangeHandler (flag = false) {
      this.selectedDiscipline = this.refinedDisciplines[this.student.disciplineId]

      if (this.selectedDiscipline) {
        if (flag) this.student.studentHasSubjectsById = this.$route.params.id ? this.editingCaseSubjectRowsHandler() : this.createCaseSubjectRowsHandler()

        // if (!this.$route.params.id) {
        //   StudentApi.generateNewRegNo(this.student.disciplineId).then((response) => {
        //     this.student.id = response.body.regNo
        //   })
        //
        //   StudentApi.generateNewSymbolNo(this.student.disciplineId).then((response) => {
        //     this.student.symbolNo = response.body.symbolNo
        //   })
        // }
      }
    },

    fetchNewSchoolId () {
      SchoolApi.getNextSchoolId(this.school.locationWardId).then((response) => {
        this.school.id = response.body.schoolId
      })
    },

    editingCaseSubjectRowsHandler () {
      // shs rows
      if (!this.originalShsArr) {
        this.originalShsArr = {}
        this.student.studentHasSubjectsById.forEach((item) => {
          this.originalShsArr[item.subjectOrder] = item
        })
      }

      let subjectRows = []

      for (let i = 1; i <= this.selectedDiscipline.subjectCount; i++) {
        if (this.originalShsArr[i]) {
          let newItem = { ...this.originalShsArr[i] }

          let ids = []

          this.selectedDiscipline.mappedSubjects[i].forEach((item) => {
            ids.push(item.subjectId)
          })

          if (ids.indexOf(newItem.subjectId) === -1) {
            newItem.subjectId = this.selectedDiscipline.mappedSubjects[i][0].subjectId
          }
          subjectRows.push(newItem)
        } else {
          subjectRows.push({
            id: null,
            subjectId: this.selectedDiscipline.mappedSubjects[i][0].subjectId,
            subjectOrder: i
          })
        }
      }

      return subjectRows
    },

    createCaseSubjectRowsHandler () {
      let subjectRows = []

      for (let i = 1; i <= this.selectedDiscipline.subjectCount; i++) {
        subjectRows.push({
          id: null,
          subjectId: this.selectedDiscipline.mappedSubjects[i][0].subjectId,
          subjectOrder: i
        })
      }

      return subjectRows
    },

    dateConversion () {
      if (this.student.dobYear && this.student.dobMonth && this.student.dobDay) {
        try {
          this.student.dob = Util.dateConversion(this.student['dobYear'].toString().padStart(4, '0') + '/' + this.student['dobMonth'].toString().padStart(2, '0') + '/' + this.student['dobDay'].toString().padStart(2, '0'))
          return
        } catch (e) {

        }
      }

      this.student.dob = null
    }
  }
}
</script>

<style scoped>

</style>



