<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Schools</h2>
      </v-flex>

      <v-flex md4>
        <v-text-field
          v-model="query.search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-flex>
      <v-flex justify-end>
        <v-btn fab :to="{ name: 'admin.schools.create' }" class="float-right">
          <v-icon>add</v-icon>
        </v-btn>
      </v-flex>
    </v-layout>

    <v-dialog
      v-model="deleteDialog"
      max-width="400"
    >
      <v-card>
        <v-card-title class="headline">Are you sure?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteDialog = false"
          >
            No
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteItem"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-data-table
      :headers="headers"
      :items="schools"
      class="elevation-1"
      :pagination.sync="query"
      :rows-per-page-items="[20, 50, 100]"
      :total-items="totalItems"
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ (query.rowsPerPage * (query.page - 1)) + props.index + 1 }}</td>
        <td>{{ props.item.id }}</td>
        <td>{{ props.item.name }}</td>
        <td>{{ studentsCount[props.item.id] }}</td>
        <td>{{ props.item.locationWardId }}</td>
        <td>{{ props.item.schoolTypeId }}</td>
        <td>
          <v-btn danger flat @click=" showDeleteDialog(props.item) ">
            <v-icon>delete</v-icon>
          </v-btn>

          <v-btn info flat @click=" loginAsSchool(props.item) ">
            <v-icon>person_outline</v-icon>
          </v-btn>

          <v-btn :to="{ name: 'admin.schools.edit', params: { id: props.item.id } }" primary flat>
            <v-icon>create</v-icon>
          </v-btn>
        </td>
      </template>
    </v-data-table>

  </div>

</template>

<script>

import SchoolApi from '../../../api/schools'
import SchoolForm from './Form'

export default {

  components: { SchoolForm },
  mounted () {
    this.getDataFromApi()
  },

  watch: {
    query: {
      handler () {
        this.getDataFromApi()
      },
      deep: true
    }
  },
  data () {
    return {
      dialog: false,
      loading: true,
      query: {
        search: null,
        rowsPerPage: 20
      },
      schools: [],
      deletingItem: null,
      deleteDialog: false,
      totalItems: null,
      studentsCount: {},
      headers: [
        {
          text: 'SN',
          align: 'left',
          sortable: false,
          value: 'SN'
        },
        {
          text: 'Code',
          align: 'left',
          sortable: true,
          value: 'id'
        },
        { text: 'Name', value: 'name' },
        { text: 'Student Count', value: 'studentCount' },
        { text: 'Ward', value: 'locationWardId', sortable: true },
        { text: 'Type', value: 'schoolTypeId', sortable: true },
        { text: 'Actions', value: 'Actions', sortable: false }
      ]
    }
  },
  methods: {
    getDataFromApi () {
      this.loading = true
      return SchoolApi.index(this.query).then((response) => {
        this.schools = response.body.schools.content
        this.query.totalItems = response.body.schools.totalElements
        this.totalItems = response.body.schools.totalElements
        this.studentsCount = response.body.studentCount
      })
    },

    deleteItem () {
      SchoolApi.delete(this.deletingItem.id).then((response) => {
        let index = this.schools.indexOf(this.deletingItem)
        this.schools.splice(index, 1)
      }).catch((err) => {
        console.log(err)
        if (err) {
          alert(err.body.message)
        }
      })

      this.deleteDialog = false
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },

    showDeleteDialog (item) {
      this.deletingItem = item
      this.deleteDialog = true
    },

    loginAsSchool (item) {
      this.$auth.impersonate({
        body: {
          schoolId: item.id
        },
        success: function () {
        },
        error: function () {
          alert('Look like school lacks school admin')
        },
        redirect: { name: 'school.students.index' }
        // etc...
      })
    }
  }
}
</script>

<style scoped>

</style>



