<template>

  <v-form v-model="valid" ref="school_form">
    <v-layout>
      <v-flex md5>
        <h3>School Data</h3>
        <v-autocomplete
          v-model="school.locationWardId"
          :items="wards"
          item-text="name"
          item-value="id"
          label="Ward Number*"
          @blur="fetchNewSchoolId"
          single-line
        ></v-autocomplete>

        <v-autocomplete
          v-model="school.schoolTypeId"
          :items="schoolTypes"
          item-text="name"
          item-value="id"
          :rules="schoolTypeRules"
          label="School Type*"
          single-line
        ></v-autocomplete>

        <v-text-field
          v-model="school.id"
          :counter="5"
          label="School Id / School Code*"
          required
          readonly
          v-show="$route.params.id"
        ></v-text-field>

        <v-text-field
          v-model="school.name"
          :rules="nameRules"
          :counter="80"
          label="Name*"
          required
          class="my-text-uppercase"
        ></v-text-field>

        <v-select
          v-model="school.schoolHasDisciplinesById"
          :items="disciplines"
          attach
          chips
          label="Disciplines*"
          item-text="name"
          item-value="id"
          multiple
          ref="discipline"
          :rules="disciplineRules"
        >

          <v-list-tile
            slot="append-item"
          >
            <v-list-tile-content>
              <v-btn @click="disciplineDoneBtn">Done</v-btn>
            </v-list-tile-content>
          </v-list-tile>
        </v-select>

        <v-btn
          :disabled="!valid"
          @click="submit"
        >
          submit
        </v-btn>
        <v-btn @click="clear">clear</v-btn>
      </v-flex>

      <v-flex md5 offset-md1>

        <v-text-field
          v-model="school.address"
          :counter="50"
          label="Address*"
          :rules="addressRules"
          ref="address"
          class="my-text-uppercase"
        ></v-text-field>

        <v-text-field
          v-model="school.email"
          :counter="30"
          label="Email*"
          type="email"
          ref="email"
          :rules="emailRules"
        ></v-text-field>

        <v-text-field
          v-model="school.fax"
          :counter="30"
          label="Fax"
          :rules="faxRules"
          type="number"
        ></v-text-field>

        <v-text-field
          v-model="school.telephone"
          :counter="10"
          label="Telephone / Mobile Number*"
          :rules="telephoneRules"
          type="number"
          v-validate="$route.params.id ? '' : 'school_telephone'"
          :error-messages="errors.collect('telephone')"
          :error="errors.has('telephone')"
          data-vv-name="telephone"
        ></v-text-field>

      </v-flex>
    </v-layout>
  </v-form>
</template>

<script>
import WardApi from '../../../api/wards'
import SchoolTypeApi from '../../../api/school-types'
import SchoolApi from '../../../api/schools'
import DisciplineApi from '../../../api/disciplines'
import SchoolTransformer from '../../../transformers/schools'
import Util from '../../../utils'

export default {
  name: 'school-form',

  created () {
    SchoolTypeApi.index().then((response) => {
      this.schoolTypes = response.body
    })

    WardApi.index().then((response) => {
      this.wards = response.body
    })

    DisciplineApi.index().then((response) => {
      this.disciplines = response.body
    })
  },

  props: {
    school: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      schoolTypes: [],
      disciplines: [],
      wards: [],
      valid: false,
      name: '',
      id: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 80) || 'Name must be less than 80 characters',
        v => (v && (/^[A-z .\-()']+$/g).test(v)) || 'Please provide valid name'
      ],

      emailRules: [
        v => !!v || 'Email is required',
        v => (v && Util.validateEmail(v)) || 'Please provide valid email'
      ],

      telephoneRules: [
        v => !!v || 'Telephone number or Mobile number is required',
        v => (v && (/(^9\d{9})$|(^0\d{8})$/).test(v)) || 'Please provide valid telephone number or mobile number'
      ],

      faxRules: [
        v => !!v === false || v === null || v.length === 0 || (v && v.test(/^0\d{8}$/)) || 'Please provide valid fax number'
      ],

      addressRules: [
        v => !!v || 'Address is required',
        v => (v && (/^[A-z, ]+$/g).test(v)) || 'Please provide valid address'
      ],

      disciplineRules: [
        v => !!v || 'Discipline is required',
        v => (v && v.length > 0) || 'Please provide Disciplines'
      ],

      schoolTypeRules: [
        v => !!v || 'School Type is required'
      ]
    }
  },

  methods: {
    clear () {
      this.$refs.school_form.reset()
    },

    disciplineDoneBtn () {
      this.$refs.discipline.isMenuActive = false
      this.$refs.discipline.isFocused = false
      this.goTo('address')
    },

    submit () {
      this.$validator.validateAll().then((res) => {
        if (res) {
          SchoolApi.save(SchoolTransformer.spring(this.school)).then((response) => {
            this.$router.push({ name: 'admin.schools.index' })
          })
        } else {
          alert('Already used username', this.errors.collect('username'))
        }
      })
    },

    fetchNewSchoolId () {
      // if (!this.$route.params.id) {
      //   SchoolApi.getNextSchoolId(this.school.locationWardId).then((response) => {
      //     this.school.id = response.body.schoolId
      //   })
      // }
    }
  }
}
</script>

<style scoped>

</style>



