import Vue from 'vue'

export default {
  index () {
    return Vue.http.get('v1/wards')
  },

  save (ward) {
    return Vue.http.post('v1/wards', ward)
  },

  delete (wardId) {
    return Vue.http.delete('v1/wards/' + wardId)
  },

  findById (wardId) {
    return Vue.http.get('v1/wards/' + wardId)
  }
}



