import Vue from 'vue'

export default {
  index () {
    return Vue.http.get('v1/subjects')
  },

  save (subject, oldId) {
    return Vue.http.post('v1/subjects', subject, { params: { oldId } })
  },

  delete (subject) {
    return Vue.http.delete('v1/subjects/' + subject)
  },

  findById (subject) {
    return Vue.http.get('v1/subjects/' + subject)
  },

  getAllSubjectGroupBySubjectOrder () {
    return Vue.http.get('v1/subjects/group-by-subject-order')
  },

  getPracticalSubjectsForCurrentlyLoggedUser () {
    return Vue.http.get('v1/school/subjects/subject-with-practical')
  },

  getSubjectsForASchool (schoolId) {
    return Vue.http.get('v1/school/subjects/subject-list', { params: { schoolId } })
  },

  getDataForSchoolListingOnSubject (subjectId) {
    return Vue.http.get('v1/subjects/' + subjectId + '/school-listing')
  }
}



