<template>
  <div>
    <v-layout>
      <v-flex md10 offset-md1>
        <h2 style="margin: 20px">Basic Field Auditing</h2>
        <v-data-table
          :headers="studentHeaders"
          :items="studentItems"
          hide-actions
          light
        >

          <template slot="headerCell" slot-scope="props">
            <v-tooltip bottom>
                <span slot="activator">
                  {{ props.header.text }}
                </span>
                      <span>
                  {{ props.header.text }}
                </span>
            </v-tooltip>
          </template>

          <template slot="items" slot-scope="props">
            <td>{{ props.item }}</td>
            <td v-for="md in Object.keys(basicFields)">
              <v-tooltip bottom
                         v-if=" (studentItems.length - props.index) === 1 ||  groupByStudentModifiedDate[studentItems[props.index + 1]][md] !== groupByStudentModifiedDate[props.item][md] ">
                <span slot="activator">{{ groupByStudentModifiedDate[props.item][md] }}</span>
                <span>{{ revisions.users[groupByStudentModifiedDate[props.item]['lastModifiedBy']].username +' [' + revisions.users[groupByStudentModifiedDate[props.item]['lastModifiedBy']].name + ']' }}</span>
              </v-tooltip>
            </td>
          </template>

        </v-data-table>
      </v-flex>

    </v-layout>

    <v-layout style="margin: 30px">

      <v-flex md12>
        <h2 style="margin: 20px">Marks Auditing</h2>
        <v-data-table
          :headers="subjectHeaders"
          :items="shsItems"
          hide-actions
        >


          <template slot="headers" slot-scope="props">
            <tr>
              <th v-for="header in props.headers" :width="header.width" :colspan="header.twoColumn ? 2 : 1">{{ header.text }}</th>
            </tr>
            <tr>
              <template v-for="header in props.headers">
                <th>{{ header.twoColumn ? 'TH' : '' }}</th>
                <th v-if="header.twoColumn">PR</th>
              </template>
            </tr>
          </template>


          <template slot="items" slot-scope="props">
            <td>{{ props.item }}</td>
            <template v-for="md in Object.keys(subjects)">
              <td>
                <v-tooltip bottom
                           v-if=" groupBySHSModifiedDate[props.item] && groupBySHSModifiedDate[props.item][md] ">
                  <span slot="activator">{{ groupBySHSModifiedDate[props.item][md].marks }}</span>
                  <span>{{ revisions.users[groupBySHSModifiedDate[props.item][md]['lastModifiedBy']].username +' [' + revisions.users[groupBySHSModifiedDate[props.item][md]['lastModifiedBy']].name + ']' }}</span>
                </v-tooltip>
              </td>
              <td>
                <v-tooltip bottom
                           v-if=" groupBySHSModifiedDate[props.item] && groupBySHSModifiedDate[props.item][md] ">
                  <span slot="activator">{{ groupBySHSModifiedDate[props.item][md].prMarks }}</span>
                  <span>{{ revisions.users[groupBySHSModifiedDate[props.item][md]['lastModifiedBy']].username +' [' + revisions.users[groupBySHSModifiedDate[props.item][md]['lastModifiedBy']].name + ']' }}</span>
                </v-tooltip>
              </td>
            </template>
          </template>

        </v-data-table>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import StudentApi from '../../../api/students'

export default {
  created () {
    StudentApi.history(this.$route.params.id).then((res) => {
      console.log(res.body)
      this.revisions = res.body
      this.groupByStudentModifiedDate = {}
      this.revisions.studentRevisions.forEach((item) => {
        this.groupByStudentModifiedDate[new Date(item.lastModifiedAt).toLocaleString()] = item
      })

      this.revisions.shsRevisions.filter(item => item.marks).forEach((item) => {
        let timestamp = new Date(item.lastModifiedAt).toLocaleString()
        if (!this.groupBySHSModifiedDate[timestamp]) {
          this.groupBySHSModifiedDate[timestamp] = {}
        }
        this.groupBySHSModifiedDate[timestamp][item.subjectId] = item

        this.subjects[item.subjectId] = this.revisions.subjectMap[item.subjectId].subjectShortcode
      })

      this.partialHeaders = Object.keys(this.basicFields).map(item => {
        return {
          text: this.basicFields[item],
          align: 'left',
          sortable: false,
          value: item
        }
      })

      this.studentHeaders = [
        {
          text: 'Date',
          align: 'left',
          sortable: false,
          value: 'date'
        }
      ].concat(this.partialHeaders)

      this.subjectHeaders = [
        {
          text: 'Date',
          align: 'left',
          sortable: false,
          value: 'date',
          twoColumn: false,
          width: '12%'
        }
      ].concat(Object.keys(this.subjects).map((item) => {
        return {
          text: this.subjects[item],
          align: 'left',
          sortable: false,
          value: item,
          twoColumn: true,
          width: '8%'
        }
      }))

      this.studentItems = Object.keys(this.groupByStudentModifiedDate)

      this.shsItems = Object.keys(this.groupBySHSModifiedDate)
    })
  },
  data () {
    return {
      revisions: [],
      groupByStudentModifiedDate: {},
      groupBySHSModifiedDate: {},
      studentHeaders: [],
      partialHeaders: [],
      studentItems: [],
      basicFields: {
        'name': 'Name',
        'fatherName': 'Father',
        'dob': 'DOB',
        'symbolNo': 'Symbol Number',
        'sex': 'Gender'
      },

      subjectHeaders: [],

      subjects: {},

      shsItems: []
    }
  },

  methods: {
    generateHeaderForStudent () {

    }
  }
}
</script>

<style scoped>

</style>



