<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Add New School</h2>
      </v-flex>
      <v-flex md1>
        <v-btn fab :to="{ name: 'admin.schools.index' }" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <school-form :school="blankSchool"></school-form>

  </div>

</template>

<script>

import SchoolForm from './Form'

export default {
  components: { SchoolForm },
  created () {
  },

  data () {
    return {
      blankSchool: {
        id: null,
        name: '',
        schoolTypeId: '',
        email: null,
        fax: null,
        telephone: null,
        pobox: null,
        url: null,
        address: null,
        schoolHasDisciplinesById: []
      }
    }
  }
}
</script>

<style scoped>

</style>



