// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import Vuetify from 'vuetify'
import 'material-design-icons-iconfont/dist/material-design-icons.css' // Ensure you are using css-loader
import './assets/style.css'
import 'vuetify/dist/vuetify.min.css'
import VeeValidate from 'vee-validate'
import Auth from '@websanova/vue-auth'
import Bus from './Bus'
import Utils from './utils'
import Mixin from './mixins'
import store from './store/index'
import Validators from './validators'
import sdfds from './gtm'

import auth from '@websanova/vue-auth/drivers/auth/bearer.js'

import authHttp from '@websanova/vue-auth/drivers/http/vue-resource.1.x.js'

import authRouter from '@websanova/vue-auth/drivers/router/vue-router.2.x.js'

VeeValidate.Validator.extend('school_telephone', Validators.school_telephone)
VeeValidate.Validator.extend('symbol_number', Validators.symbol_number)


Vue.use(Vuetify)
Vue.use(VueResource)
Vue.use(VeeValidate)

Vue.router = router

Vue.mixin(Mixin)

Vue.use(Auth, {
  auth: auth,
  http: authHttp,
  router: authRouter,
  parseUserData: (data) => {
    Utils.sleep(100).then(() => {
      Bus.$emit('user.fetched', data)
    })
    return data
  },
  refreshData: {
    url: 'api/auth/refresh',
    method: 'GET',
    enabled: true,
    interval: 30,
    error: (err) => {
      Bus.$emit('error.on.refresh', err)
    }
  },
  impersonateData: {
    url: 'api/v1/admin/impersonate',
    method: 'POST',

  },
  unimpersonateData:  {url: 'auth/unimpersonate', method: 'POST', redirect: '/admin/dashboard', makeRequest: false},
  rolesVar: 'role',
  tokenStore: ['localStorage', 'cookie']
})
Vue.http.options.root = window.myUrl;

Vue.config.productionTip = false
Vue.prototype.$bus = Bus

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router: router,
  store: store,
  template: '<App/>',
  components: { App }
})



