<template>
  <div>
    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit Subject</h2>
      </v-flex>
      <v-flex md2>
        <v-btn fab :to="{ name: 'admin.subjects.index' }"  class="float-right" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <subject-form :subject="subject"></subject-form>
  </div>
</template>

<script>
import SubjectApi from '../../../api/subjects'
import SubjectForm from './Form'

export default {

  components: { SubjectForm },
  data () {
    return {
      subject: {
        hasPractical: false
      }
    }
  },

  watch: {
    '$route' (to, from) {
      this.created()
    }
  },

  created () {
    SubjectApi.findById(this.$route.params.id).then((response) => {
      this.subject = response.body
      this.hasPractical = response.body.hasPractical
    })
  }
}
</script>



