import Vue from 'vue'

export default {
  index (query) {
    return Vue.http.post('v1/schools/index', query)
  },

  save (school) {
    return Vue.http.post('v1/schools', school)
  },

  delete (schoolId) {
    return Vue.http.delete('v1/schools/' + schoolId)
  },

  getNextSchoolId (wardId) {
    return Vue.http.get('v1/schools/generate-id', { params: { wardId } })
  },

  findById (schoolId) {
    return Vue.http.get('v1/schools/' + schoolId)
  },

  getDisciplinesForLoggedUserSchool () {
    return Vue.http.get('v1/school/disciplines')
  },

  updateProfile (data) {
    return Vue.http.post('v1/school/profile', data)
  },

  getProfile () {
    return Vue.http.get('v1/school/profile')
  }
}



