import Vue from 'vue'

export default {
  index (query) {
    return Vue.http.post('v1/admin/users/index', query)
  },

  save (user) {
    return Vue.http.post('v1/admin/users', user)
  },

  changePassword (data) {
    return Vue.http.post('v1/admin/users/change-password', data)
  },

  delete (user) {
    return Vue.http.delete('v1/admin/users/' + user)
  },

  findById (user) {
    return Vue.http.get('v1/admin/users/' + user)
  },

  findAllPermissions () {
    return Vue.http.get('v1/admin/users/permissions')
  },

  checkUsernameExistsOrNot (username) {
    return Vue.http.get('v1/admin/users/username-exists-or-not', { params: { username } })

  }
}



