<template>
  <div>

    <v-layout row style="margin-bottom: 30px;">
      <v-flex md3 mb20>
        <h2>Marks Entry (Slip Wise)</h2>
      </v-flex>
      <v-flex md2 mb100>
        <v-btn @click="downloadReport()">Download Slip Form</v-btn>
      </v-flex>
    </v-layout>
    <v-form v-model="valid">
      <v-layout row>
        <v-flex md2 offset-md2>
          <v-text-field
            v-model="slipNumber"
            :rules="slipNumberRules"
            label="Slip Number"
          >
          </v-text-field>
        </v-flex>

        <v-flex md2>
          <v-btn @click="loadEntries" :disabled="!valid || saving">Load</v-btn>
        </v-flex>

        <v-flex>
          <h3>Subject: {{ entries.length > 0 ? entries[0].subjectName : '-' }}</h3>
        </v-flex>

      </v-layout>

      <div>
        <v-flex md4 offset-md4>
          <v-data-table
            :headers="headers"
            :items="entries"
            hide-actions
          >
            <template slot="items" slot-scope="props">
              <td>{{ props.index + 1 }}</td>
              <td>{{ props.item.symbolNo }}</td>
              <td>
                <v-text-field
                  v-model="props.item.marks"
                  label="Marks"
                  :rules="marksRules"
                  :ref=" 'marks-' + props.index "

                  @keyup="markEntered(props.index)"
                >

                </v-text-field>
              </td>
            </template>
          </v-data-table>
        </v-flex>
      </div>
      <v-flex md2 offset-md2>
        <v-btn @click="saveEntries" :disabled="!valid || saving">Save</v-btn>
      </v-flex>
    </v-form>
  </div>
</template>

<script>
import ReportApi from '../../../api/downloads'
import MarksEntryApi from '../../../api/marks-entry'

export default {
  name: 'slip-wise',

  data () {
    return {
      slipNumber: null,
      slipNumberRules: [
        v => !!v || 'Slip Number is required',
        v => (v && v.toString().length === 7) || 'Slip number should be 7 digit long'
      ],
      valid: false,
      saving: false,
      marksRules: [
        v => !v || parseInt(v) <= this.entries[0].fullMarks || 'Marks should be less than full marks '
      ],
      headers: [
        {
          text: 'SN',
          align: 'left',
          sortable: false,
          value: 'sn'
        },
        {
          text: 'Symbol Number',
          align: 'left',
          sortable: false,
          value: 'symbolNo'
        },
        {
          text: 'Marks',
          align: 'left',
          sortable: false,
          value: 'marks',
          width: '20%'
        }
      ],

      entries: []
    }
  },

  methods: {
    downloadReport () {
      ReportApi.downloadSlipForm()
    },

    loadEntries () {
      MarksEntryApi.loadDataBySlipNumber(this.slipNumber).then((res) => {
        this.entries = res.body
        this.goTo('marks-0')
      })
    },

    saveEntries () {
      this.saving = true
      if (this.entries.length > 0) {
        MarksEntryApi.save(this.entries).then((res) => {
          this.saving = false
          this.slipNumber = parseInt(this.slipNumber) + 1
          this.loadEntries()
        })
      }
    },

    markEntered (index) {
      console.log(this.entries[index].marks, index)
      if (this.entries[index].marks && this.entries[index].marks.toString().length === 2 && this.entries.length > (index - 1)) {
        this.goTo('marks-' + (index + 1))
      }
    }
  }
}
</script>

<style scoped>

</style>



