<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit Centre</h2>
      </v-flex>
      <v-flex md1>
        <v-btn fab :to="{ name: 'admin.centres.index' }" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <centre-form :centre="centre"></centre-form>

  </div>

</template>

<script>
import CentreForm from './Form'
import CentreApi from '../../../api/centres'
import CentreTransformer from '../../../transformers/centres'


export default {
  components: { CentreForm },
  watch: {
    '$route' (to, from) {
      this.created()
    }
  },

  created () {
    CentreApi.findById(this.$route.params.id).then((response) => {
      this.centre = CentreTransformer.vue(response.body)
    })
  },

  data () {
    return {
      centre: {}
    }
  }

}
</script>

<style scoped>

</style>



