<template>
  <div>
    <v-layout row style="margin-bottom: 30px;">
      <v-flex md2 mb20>
        <h2>Marksheet Print</h2>
      </v-flex>
    </v-layout>

      <v-layout row style="margin-bottom: 30px;">
      <v-flex md2 offset-md2 mb100>
        <v-text-field
          label="Symbol Number"
          v-model="symbolNumber"
        ></v-text-field>
      </v-flex>

      <v-flex md2 offset-md1 mb100><v-btn @click="loadHtml('student')">Print Marksheet For Single Student</v-btn></v-flex>
    </v-layout>

    <v-layout row style="margin-bottom: 30px;">
      <v-flex md2 offset-md2 mb100>
        <v-text-field
          label="School Id"
          v-model="schoolId"
        ></v-text-field>
      </v-flex>

      <v-flex md2 offset-md1 mb100><v-btn @click="loadHtml('school')">Print Marksheet for Whole School</v-btn></v-flex>
    </v-layout>



    <v-layout row style="margin-bottom: 30px;">
      <v-flex md2 offset-md2 mb100>
      </v-flex>
      <v-flex md2 offset-md1 mb100><v-btn @click="loadHtml('all_supp')">Print Marksheet for Supplementary Students</v-btn></v-flex>
    </v-layout>

  </div>
</template>

<script>

import MarkSheetApi from '../../../api/marksheet'
export default {
  name: 'marksheet-print',
  data () {
    return {
      symbolNumber: null,
      schoolId: null
    }
  },

  methods: {
    printDiv (printContents) {
      let w = window.open()

      w.document.write(printContents)
      w.document.write('<scr' + 'ipt type="text/javascript">' + 'window.onload = function() { window.print(); window.close(); };' + '</sc' + 'ript>')

      w.document.close() // necessary for IE >= 10
      w.focus() // necessary for IE >= 10

      return true
    },

    loadHtml (type) {
      MarkSheetApi.print({ type: type, identifier: type === 'school' ? this.schoolId : this.symbolNumber }).then((res) => {
        this.printDiv(res.body)
      })
    }
  }
}
</script>

<style scoped>

</style>



