<template>
  <div>
    <v-layout row style="margin-bottom: 30px;">
      <v-flex md2 mb20>
        <h2>Downloads</h2>
      </v-flex>
    </v-layout>

    <v-layout row>
      <v-flex md2 offset-md2 >
        <v-text-field
          label="School Id"
          v-model="schoolId"
        ></v-text-field>
      </v-flex>
      <v-flex md2 offset-md1>
        <v-btn @click="downloadLedger(schoolId)">Download Ledger for School</v-btn>
      </v-flex>
    </v-layout>

    <hr>

    <v-layout row >
      <v-flex md1 offset-md2>
        <v-text-field
          label="Extra Percentage"
          v-model="extraPercentage"></v-text-field>
      </v-flex>
      <v-flex md6 offset-md2 >
        <v-btn @click="downloadCentreWiseLogisticsReport()">Download Centre Wise Logistics Report</v-btn>
      </v-flex>
    </v-layout>

    <hr>

    <v-layout row>
      <v-flex md2 offset-md5 >
        <v-btn @click="downloadLedger()">Download Ledger for All Schools</v-btn>
      </v-flex>
    </v-layout>

    <hr>

    <v-layout row >
      <v-flex md2 offset-md5>
        <v-btn @click="downloadSchoolWiseMarksEntryForm()">Download School Wise Marks Entry</v-btn>
      </v-flex>
    </v-layout>

    <hr>

    <v-layout row >
      <v-flex md2 offset-md5>
        <v-btn @click="downloadSupSchoolWiseMarksEntryForm()">Download School Wise Supp Marks Entry</v-btn>
      </v-flex>
    </v-layout>

    <hr>

    <v-layout row >
      <v-flex md6 offset-md5>
        <v-btn @click="downloadSubjectRujuReport()">Download Subject Ruju Report</v-btn>
      </v-flex>
    </v-layout>
    <hr>
    <v-layout row >
      <v-flex md6 offset-md5 >
        <v-btn @click="downloadDisciplineRujuReport()">Download Discipline Ruju Report</v-btn>
      </v-flex>
    </v-layout>

    <hr>
    <v-layout row >
      <v-flex md6 offset-md5 >
        <v-btn @click="downloadSchoolRujuReport()">Download School Ruju Report</v-btn>
      </v-flex>
    </v-layout>
    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadCentreWiseSchoolReport()">Download Centre Wise School Report</v-btn>
      </v-flex>
    </v-layout>
    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadRoutine()">Download Routine</v-btn>
      </v-flex>
    </v-layout>

    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadControlListReport()">Download Control List</v-btn>
      </v-flex>
    </v-layout>

    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadSuppControlListReport()">Download Supp Control List</v-btn>
      </v-flex>
    </v-layout>


    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadPracticalEntryFormReport()">Download Practical Marks Entry From</v-btn>
      </v-flex>
    </v-layout>

    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadMarksMissingRecords()">Download Marks Missing Records Information</v-btn>
      </v-flex>
    </v-layout>


    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadSubjectWise()">Download Subject Wise Grade</v-btn>
      </v-flex>
    </v-layout>


    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadSchoolWiseSubject()">Download School wise Subject Grade</v-btn>
      </v-flex>
    </v-layout>


    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadSchoolWiseDetailReport()">Download School wise detail report</v-btn>
      </v-flex>
    </v-layout>

    <hr>
    <v-layout row>
      <v-flex md6 offset-md5>
        <v-btn @click="downloadSymbolNumbersReport()">Download Symbol Number List</v-btn>
      </v-flex>
    </v-layout>
    <hr>

    <v-layout row>
      <v-flex md2 offset-md2 >
        <v-text-field
          label="School Id"
          v-model="schoolIdExcel"
        ></v-text-field>
      </v-flex>
      <v-flex md2 offset-md1>
        <v-btn @click="downloadLedgerExcel()">Download Ledger Excel Format</v-btn>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>

import DownloadsApi from '../../../api/downloads'

export default {
  name: 'index',
  data () {
    return {
      schoolId: null,
      extraPercentage: null,
      schoolIdExcel: undefined
    }
  },

  methods: {
    downloadLedger (schoolId = null) {
      if (schoolId) {
        DownloadsApi.downloadLedger({ schoolId })
      } else {
        DownloadsApi.downloadLedger()
      }
    },

    downloadSchoolWiseMarksEntryForm () {
      DownloadsApi.downloadSchoolWiseMarksEntryForm()
    },

    downloadCentreWiseLogisticsReport () {
      DownloadsApi.downloadLogisticsReport({
        extraPercentage: this.extraPercentage
      })
    },

    downloadSubjectRujuReport () {
      DownloadsApi.downloadSubjectRujuReport()
    },

    downloadSchoolRujuReport () {
      DownloadsApi.downloadSchoolRujuReport()
    },

    downloadDisciplineRujuReport () {
      DownloadsApi.downloadDisciplineRujuReport()
    },

    downloadCentreWiseSchoolReport () {
      DownloadsApi.downloadCentreWiseSchoolReport()
    },

    downloadControlListReport () {
      DownloadsApi.downloadControlListReport()
    },

    downloadSuppControlListReport () {
      DownloadsApi.downloadSuppControlListReport()
    },


    downloadPracticalEntryFormReport () {
      DownloadsApi.downloadPracticalEntryFormReport()
    },

    downloadMarksMissingRecords () {
      DownloadsApi.downloadMarksMissingRecords()
    },

    downloadRoutine () {
      DownloadsApi.downloadRoutine()
    },

    downloadSubjectWise () {
      DownloadsApi.downloadSubjectWiseReport()
    },

    downloadSchoolWiseSubject () {
      DownloadsApi.downloadSchoolSubjectWiseReport()
    },

    downloadSchoolWiseDetailReport () {
      DownloadsApi.downloadSchoolWiseDetailReport()
    },

    downloadSymbolNumbersReport () {
      DownloadsApi.downloadSymbolNumbersReport()
    },

    downloadLedgerExcel () {
      DownloadsApi.downloadLedgerExcel(this.schoolIdExcel)
    },

    downloadSupLedger () {
      DownloadsApi.downloadSupLedger()
    },

    downloadSupSchoolWiseMarksEntryForm () {
      DownloadsApi.downloadSupSchoolWiseMarksEntryForm()
    }

  }
}
</script>

<style scoped>
  hr {
    border: 1;
    clear:both;
    display: block;
    width: 70%;
    margin: auto;
    margin-bottom: 10px;
    margin-top: 10px;
    background-color: black;
  }
</style>



