<template>

  <v-flex md6>
    <v-form v-model="valid" ref="ward_form">
      <v-text-field
        v-model="ward.id"
        :rules="wardNumberRules"
        :counter="2"
        label="Ward Number"
        required
      ></v-text-field>

      <v-text-field
        v-model="ward.name"
        :rules="nameRules"
        :counter="30"
        label="Name"
        required
      ></v-text-field>

      <v-btn
        :disabled="!valid"
        @click="submit"
      >
        submit
      </v-btn>
      <v-btn @click="clear">clear</v-btn>
    </v-form>
  </v-flex>

</template>

<script>
  import WardApi from '../../../api/wards'
export default {
  name: 'ward-form',

  props: {
    ward: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      valid: false,
      name: '',
      id: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 30) || 'Name must be less than 10 characters'
      ],
      wardNumberRules: [
        v => !!v || 'Ward Number is required',
        v => (v && v.length <= 2) || 'Ward Number must be less than 2 characters'
      ]
    }
  },

  methods: {
    clear () {
      this.$refs.ward_form.reset()
    },

    submit () {
      WardApi.save(this.ward).then((response) => {
        this.$router.push({ name: 'admin.wards.index' })
      })
    }
  }
}
</script>

<style scoped>

</style>



