export default {
  spring (school) {
    let newSchoolHasDisciplines = []
    school.schoolHasDisciplinesById.forEach((item) => {
      newSchoolHasDisciplines.push({
        id: null,
        schoolId: null,
        disciplineId: item
      })
    })
    let newSchool = { ...school }
    newSchool.schoolHasDisciplinesById = newSchoolHasDisciplines
    return newSchool
  },

  vue (school) {
    school.schoolHasDisciplinesById = school.schoolHasDisciplinesById.map(item => item.disciplineId)
    return school
  }
}



