<template>
  <input
    v-model="pickedDate"
    :id="errors.vmId"
    @change="blurEvent"
    type="text"
  />
</template>
<script>
export default {
  name: 'nepali-date-picker',
  mounted () {
    let innerThis = this
    window.$(innerThis.$el).nepaliDatePicker({
      onChange: function () {
        innerThis.$emit('input', innerThis.$el.value)
      }
    })
  },

  beforeDestroy: function () {
    window.$(this.$el).remove()
  },

  props: ['value'],

  data () {
    return {
      pickedDate: this.value
    }
  },

  methods: {
    blurEvent (event) {
      this.$nextTick(() => {
        this.$emit('input', event.target.value)
      })
    }
  }
}
</script>

<style scoped>
  input {
    /*display: none;*/
  }

</style>



