<template>

  <div>

    <v-dialog v-model="printDialog" fullscreen>
      <v-btn @click="printDialog = false" style="float: right">
        <v-icon>close</v-icon>
      </v-btn>
      <admit-card :query="query" :printDialog="printDialog" style="background: white"  ></admit-card>
    </v-dialog>

    <v-dialog
      v-model="deleteDialog"
      max-width="400"
    >
      <v-card>
        <v-card-title class="headline">Are you sure?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteDialog = false"
          >
            No
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteItem"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-layout row wrap mb40>
      <v-flex md2 mb20>
        <h2>Students</h2>
      </v-flex>

      <v-flex md4>
        <v-text-field
          v-model="query.search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-flex>
      <v-flex justify-end>
        <v-btn fab class="float-right" @click=" printDialog = true "><v-icon>print</v-icon></v-btn>
        <v-btn fab :to="{ name: 'school.students.create' }" class="float-right"><v-icon>add</v-icon></v-btn>
        <v-btn class="float-right" @click="downloadRujuReport">DOWNLOAD RUJU REPORT</v-btn>
        <v-btn class="float-right" :to="{ name: 'school.supp-list' }">SUPP LIST</v-btn>
        <v-btn v-show="$store.state.core.metas.th_marks_entry_school_side === 'true' || $store.state.core.metas.result_published === 'true' " class="float-right" @click="downloadLedgerReport">DOWNLOAD LEDGER</v-btn>
      </v-flex>
    </v-layout>

    <v-data-table
      :headers="headers"
      :items="students"
      class="elevation-1"
      :pagination.sync="query"
      :rows-per-page-items="[25, 50, 100, 500]"
      :total-items="query.totalItems"
    >
      <template slot="headerCell" slot-scope="props">
        <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
          <span>
          {{ props.header.text }}
        </span>
        </v-tooltip>
      </template>
      <template slot="items" slot-scope="props">
        <td>{{ (query.rowsPerPage * (query.page - 1)) + props.index + 1 }}</td>
        <td>{{ props.item.id }}</td>
        <td>{{ props.item.fullName }}</td>
        <td>{{ props.item.sex === 1 ? 'M' : 'F' }}</td>
        <td>{{ props.item.fatherName }}</td>
        <td>{{ props.item.motherName }}</td>
        <td>
          <v-btn danger flat  @click=" showDeleteDialog(props.item) " ><v-icon>delete</v-icon></v-btn>
          <v-btn :to="{ name: 'school.students.edit', params: { id: props.item.id } }" primary flat><v-icon>create</v-icon></v-btn>
          <v-btn :to="{ name: 'school.students.detail', params: { id: props.item.id } }" primary flat><v-icon>remove_red_eye</v-icon></v-btn>


        </td>
      </template>
    </v-data-table>

  </div>

</template>

<script>

import StudentApi from '../../../api/students'
import AdmitCard from './AdmitCard'
import DownloadApi from '../../../api/downloads'

export default {
  components: { AdmitCard },
  mounted () {
    this.getDataFromApi()
  },

  watch: {
    query: {
      handler () {
        this.getDataFromApi()
      },
      deep: true
    }
  },
  data () {
    return {
      printDialog: false,
      dialog: false,
      deleteDialog: false,
      loading: true,
      students: [],
      query: {
        search: null,
        rowsPerPage: 25,
        descending: false,
        page: 1,
        sortBy: 'id',
        totalItems: null
      },
      headers: [
        {
          text: 'SN',
          align: 'left',
          sortable: false,
          value: 'SN',
          width: '2%'
        },
        {
          text: 'Reg No',
          align: 'left',
          sortable: true,
          value: 'id'
        },
        { text: 'Name', value: 'fName' },
        { text: 'Gender', value: 'sex' },
        { text: 'Father Name', value: 'fatherName' },
        { text: 'Mother Name', value: 'motherName' },
        { text: 'Actions', value: 'Actions', sortable: false }
      ]
    }
  },
  methods: {
    getDataFromApi () {
      this.loading = true
      return StudentApi.index(this.query).then((response) => {
        this.students = response.body.content
        this.query.totalItems = response.body.totalElements
      })
    },

    deleteItem () {
      let item = this.deletingItem
      StudentApi.delete(item.id).then((response) => {
        let index = this.students.indexOf(item)
        this.students.splice(index, 1)
        this.deleteDialog = false
      })
    },

    showDeleteDialog (item) {
      this.deletingItem = item
      this.deleteDialog = true
    },

    downloadRujuReport () {
      DownloadApi.downloadStudentRujuReport()
    },

    downloadLedgerReport () {
      DownloadApi.downloadStudentLedgerForSingleSchoolReport()
    }
  }
}
</script>

<style >
  .print-dialog {
    z-index: 99999999999999 !important;
    background: white !important;
  }
</style>



