<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md8 mb20>
        <h2>Routines</h2>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex md10>
        <v-form v-model="valid" ref="discipline_form">

          <div v-for=" (routine, index) in routines ">

            <v-layout>
              <v-flex md2 >
                <v-text-field
                  v-model="routine.examDate"
                  label="EXAM DATE"
                ></v-text-field>
              </v-flex>

              <v-flex offset-md2 md6>
                <v-autocomplete
                  v-model="routine.subjects"
                  :items="subjects"
                  chips
                  item-text="name"
                  item-value="id"
                  multiple
                  @change="changeHandler"
                  :rules="subjectRules"
                  label="SUBJECTS"
                >
                  <template
                    slot="selection"
                    slot-scope="data"
                  >
                    <v-chip
                      :selected="data.selected"
                      close
                      class="chip--select-multi"
                      @input="removeSelectedSubject(data.item, index)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                  <template
                    slot="item"
                    slot-scope="data"
                  >

                    <template v-if="typeof data.item !== 'object'">
                      <v-list-tile-content v-text="data.item"></v-list-tile-content>
                    </template>
                    <template v-else>
                      <v-list-tile-content>
                        <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                      </v-list-tile-content>
                    </template>
                  </template>

                </v-autocomplete>
              </v-flex>

              <v-flex md1>
                <v-btn btn class="error" @click="removeItem(index)"><v-icon>close</v-icon></v-btn>
              </v-flex>
            </v-layout>
          </div>

          <v-btn @click="addRoutine"><v-icon>add</v-icon></v-btn>
          <v-btn
            :disabled="!valid"
            @click="submit"
          >
            submit
          </v-btn>
        </v-form>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import SubjectApi from '../../api/subjects'
import RoutineApi from '../../api/routines'

export default {
  name: 'routines',
  created () {
    SubjectApi.index().then((res) => {
      this.subjects = res.body
      RoutineApi.index().then((re) => {
        if (re.body.length > 0) {
          let subjects = re.body.map((item) => {
            return item.id
          })

          this.subjects.forEach((item) => {
            if (subjects.indexOf(item.id) !== -1) { item.disabled = true }
          })
          this.routines = this.mapData(re.body)
        } else {
          this.addRoutine()
        }
      })
    })
  },

  data () {
    return {
      subjects: [],
      routines: [],
      subjectRules: [
        v => !!v || 'At least one Subject is required',
        v => (v && v.length > 0) || 'At least one Subject is required'
      ],
      valid: false
    }
  },

  methods: {
    mapData (routines) {
      let items = {}
      routines.forEach((item) => {
        if (items[item.examDate]) {
          items[item.examDate].push(item.subjectId)
        } else {
          items[item.examDate] = [item.subjectId]
        }
      })

      let itemsArray = []

      for (let key in items) {
        itemsArray.push({
          examDate: key,
          subjects: items[key]
        })
      }

      return itemsArray
    },

    addRoutine () {
      this.routines.push({
        examDate: null,
        subjects: []
      })
    },

    removeItem (index) {
      this.routines.splice(index)
    },

    changeHandler (items) {
      this.subjects.forEach((item) => {
        if (items.indexOf(item.id) !== -1) {
          item.disabled = true
        }
      })
    },

    removeSelectedSubject (item, index) {
      let indexOfSubjectId = this.routines[index].subjects.indexOf(item)
      this.routines[index].subjects.splice(indexOfSubjectId, 1)
      item.disabled = false
    },

    mappingBeforeSave() {
      let items = []
      this.routines.forEach((item) => {
        item.subjects.forEach((innerItem) => {
          items.push({
            examDate: item.examDate,
            subjectId: innerItem
          })
        })
      })

      return items
    },

    submit () {
      RoutineApi.save(this.mappingBeforeSave()).then((res) => {
        alert('Successfully saved!')
      })
    }
  }
}
</script>

<style scoped>

</style>



