<script>
import { Pie, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins

export default {
  extends: Pie,
  mixins: [reactiveProp],
  name: 'grade-pie-chart',
  // props: {
  //   chartData: {
  //     type: Array,
  //     required: true
  //   }
  // },
  computed: {
    requiredData () {
      let ranges = this.chartData[0]
      let data = this.chartData[1]
      let datasets = []
      // console.log(ranges)
      ranges.forEach(item => {
        let total = 0;
        ['male', 'female'].forEach(gender => {
          total += data[gender] && data[gender][item] ? data[gender][item] : 0
        })
        datasets.push(total)
      })

      return {
        labels: ranges,
        data: datasets
      }
    }
  },

  mounted () {
    this.renderChart(this.chartData, {
      responsive: true,
      // layout: {
      //   padding: '20px'
      // },
      // scales: {
      //   yAxes: [{
      //     ticks: {
      //       beginAtZero: true
      //     }
      //   }]
      // },
      title: {
        display: true,
        text: 'Pie Chart Grade Wise',
        position: 'bottom'
      }
    })
  }
}
</script>



