<template>

  <div>

    <v-layout row wrap mb40 style="margin-bottom: 20px">
      <v-flex md2 mb20>
        <h2>Disciplines</h2>
      </v-flex>

      <v-flex md4>
        <v-text-field
          v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-flex>
      <v-flex justify-end>
        <v-btn v-if=" $auth.user().username === 'superuser' " fab :to="{ name: 'admin.disciplines.create' }" class="float-right"><v-icon>add</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <v-dialog
      v-model="dialog"
      max-width="400"
    >
      <v-card>
        <v-card-title class="headline">Are you sure?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="dialog = false"
          >
            No
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="deleteItem"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-layout>
      <v-flex md8 offset-md2>
        <v-data-table
          :headers="headers"
          :items="items"
          class="elevation-1"
          :rows-per-page-items="[-1]"
          :search="search"
          hide-actions
        >
          <template slot="headerCell" slot-scope="props">
            <v-tooltip bottom>
        <span slot="activator">
          {{ props.header.text }}
        </span>
              <span>
          {{ props.header.text }}
        </span>
            </v-tooltip>
          </template>
          <template slot="items" slot-scope="props">
            <td>{{ props.item.id }}</td>
            <td>{{ props.item.name }}</td>
            <td v-if=" $auth.user().username === 'superuser' " ><v-btn danger flat @click=" showDeleteDialog(props.item) "><v-icon>delete</v-icon></v-btn><v-btn :to="{ name: 'admin.disciplines.edit', params: { id: props.item.id } }" primary flat><v-icon>create</v-icon></v-btn></td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </div>

</template>

<script>

  import DisciplineApi from '../../../api/disciplines'
export default {
  created () {
    DisciplineApi.index().then((response) => {
      this.items = response.body
    })

    if (this.$auth.user().username === 'superuser') {
      this.headers.push({ text: 'Actions', value: 'actions', width: '30%', sortable: false })
    }
  },

  data () {
    return {
      search: '',
      dialog: false,
      deletingItem: null,
      headers: [
        {
          text: 'Id',
          align: 'left',
          sortable: true,
          value: 'id'
        },
        { text: 'Name', value: 'name' }
      ],
      items: []
    }
  },

  methods: {
    deleteItem () {
      DisciplineApi.delete(this.deletingItem.id).then((response) => {
        let index = this.items.indexOf(this.deletingItem)
        this.items.splice(index, 1)
        this.dialog = false

      }).catch((err) => {
        this.dialog = false
        alert('Discipline is attached with school')
      })
    },

    showDeleteDialog (item) {
      this.deletingItem = item
      this.dialog = true
    }
  }
}
</script>

<style scoped>

</style>



