import Vue from 'vue'
import Router from 'vue-router'

import adminRoutes from './admin'
import generalRoutes from './general'
import schoolRoutes from './school'

import store from '../store'

import ChangePassword from '../pages/auth/ChangePassword'

Vue.use(Router)

const router = new Router({
  routes: [

    ...adminRoutes,
    ...generalRoutes,
    ...schoolRoutes,

    {
      component: ChangePassword,
      meta: {
        auth: true
      },
      path: '/auth/change-password',
      name: 'auth.change-password'
    }
  ]
})

router.beforeEach((to, from, next) => {
  console.log(to.meta)
  if (to.meta.middleware && to.meta.middleware.includes('changepassword')) {
    return changepassword(next)
  }

  next()
})

// TODO separate middleware in different file
function changepassword (next) {
  if (store.state.core.auth.user.forcePasswordChange) {
    return next({
      name: 'auth.change-password'
    })
  }

  next()
}

export default router



