<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Edit School</h2>
      </v-flex>
      <v-flex md1>
        <v-btn fab :to="{ name: 'admin.schools.index' }" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <school-form :school="school"></school-form>

  </div>

</template>

<script>
import SchoolForm from './Form'
import SchoolApi from '../../../api/schools'
import SchoolTransformer from '../../../transformers/schools'

export default {
  components: { SchoolForm },
  watch: {
    '$route' (to, from) {
      this.created()
    }
  },

  created () {
    SchoolApi.findById(this.$route.params.id).then((response) => {
      this.school = SchoolTransformer.vue(response.body)
    })
  },

  data () {
    return {
      school: {}
    }
  }

}
</script>

<style scoped>

</style>



