<template>
  <v-form v-model="valid" ref="school_form">
    <v-layout>
      <v-flex md5>
        <h3>Change Password</h3>

        <v-text-field
          v-model="user.name"
          label="Name"
          class="my-text-uppercase"
          readonly
          outline
        ></v-text-field>

        <v-text-field
          v-model="user.username"
          required
          label="Username"
          readonly
          outline
        >
        </v-text-field>

        <v-text-field
          v-model="password"
          type="password"
          required
          label="Password"
          :rules="passwordRules"
        >
        </v-text-field>

        <v-btn
          :disabled="!valid"
          @click="changePassword"
        >
          submit
        </v-btn>
      </v-flex>
    </v-layout>
  </v-form>
</template>

<script>
import UserApi from '../../../api/users'

export default {

  created () {
    UserApi.findById(this.$route.params.id).then((response) => {
      this.user = response.body
    })
  },

  watch: {
    '$route' (to, from) {
      this.created()
    }
  },

  data () {
    return {
      valid: false,
      password: null,
      user: {},
      passwordRules: [
        v => !!v || 'Password is required'
      ]
    }
  },
  methods: {
    changePassword () {
      UserApi.changePassword({
        userId: this.user.id,
        password: this.password
      }).then((res) => {
        alert('Successfully changed')
      }).catch((err) => {
        alert('Something Went Wrong')
      })
    }
  }
}
</script>

<style scoped>

</style>



