<template>

  <div>

    <v-layout row wrap mb40>
      <v-flex md10 mb20>
        <h2>Add New Discipline</h2>
      </v-flex>
      <v-flex md2>
        <v-btn fab :to="{ name: 'admin.disciplines.index' }"  class="float-right" ><v-icon>arrow_back</v-icon></v-btn>
      </v-flex>
    </v-layout>

    <discipline-form :discipline="blankDiscipline"></discipline-form>
  </div>

</template>

<script>

import DisciplineForm from './Form'

export default {

  components: { DisciplineForm },
  created () {
  },

  data () {
    return {
      blankDiscipline: {
        id: null,
        name: '',
        disciplineHasSubjectsById: []
      }
    }
  }
}
</script>

<style scoped>

</style>



