import Vue from 'vue'

export default {
  loadDataForPracticalMarksEntry (query) {
    return Vue.http.post('v1/school/practical-marks/load-data', query)
  },

  savePracticalMarks (data) {
    return Vue.http.post('v1/school/practical-marks', data)
  },

  getMarksList () {
    return Vue.http.get('v1/school/practical-marks/marks-list')
  }
}



